import React, { useCallback, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import './style.scss';
import FiatSidebar from './components/FiatSidebar';
import FiatContent from './components/FiatContent';
import { 
  selectCoinList, 
  selectCurrencyList, 
  selectCountryList ,
} from 'redux/appSlide';
import { getAdvertList, selectAdvertList } from 'redux/advertSlide';
import { useDispatch, useSelector } from 'react-redux';
import { trans } from 'config/LinguiConfig';
import { t } from '@lingui/macro';
import { DEFAULT_COUNTRY_CODE, DEFAULT_COUNTRY_ID, DEFAULT_CURRENCY_ID, DEFAULT_CURRENCY_NAME } from 'constants/app';
import { PAYMENT_TYPE } from 'constants/payment';
import { ADVERT_TYPE } from 'constants/advert';
import GlobalStore from 'constants/global';
import LocalStorage from 'config/localStorage';
import { useParams, useSearchParams } from 'react-router-dom';
import {isEmpty} from 'utils/validator';

Fiat.propTypes = {
  type: PropTypes.number
};

Fiat.defaultProps = {
  type: ADVERT_TYPE.BUY
}

// type: number,
// currentPage: number,
// coinId: number,
// amount?: number,
// countryId: number,
// currencyId?: number,
// paymentType?: number,
// subPaymentType?: number

function Fiat(props) {
  // const [searchParams, setSearchParams] = useSearchParams();

  const dispatch = useDispatch();
  const { type } = props;
  const { id } = useParams();

  const coinList = useSelector(selectCoinList);
  const currencyList = useSelector(selectCurrencyList);
  const countryList = useSelector(selectCountryList);
  const advertList = useSelector(selectAdvertList);

  const DEFAULT_GIFTCARD_PAYMENT_TYPE = {
    fcoinid: -1,
    coinName: trans(t`Mọi phương thức thanh toán`),
  };
  const DEFAULT_SUB_PAYMENT_TYPE = { fid: -1, fname: trans(t`Mọi phương thức thanh toán`) };
  const DEFAULT_GIFTCARD_TYPE = { fid: -1, fname: trans(t`Mọi loại Gift Card`) };
  const DEFAULT_COUNTRY = {
    id: DEFAULT_COUNTRY_ID,
    code: DEFAULT_COUNTRY_CODE,
    name: trans(t`Tất cả quốc gia`),
  };
  const DEFAULT_CURRENCY = {
    id: DEFAULT_CURRENCY_ID,
    name: DEFAULT_CURRENCY_NAME,
    nameStr: trans(t`Tất cả tiền tệ`),
  };

  const [filter, setFilter] = useState({
    coin: (coinList && coinList.find((i) => i.coinName.toLowerCase() === id.toLowerCase())),
    currentPage: 1,
    onLine: false,
    subPaymentType: DEFAULT_SUB_PAYMENT_TYPE,
    giftCardPaymentType: DEFAULT_GIFTCARD_PAYMENT_TYPE,
    giftCardType: DEFAULT_GIFTCARD_TYPE,
    currency: (currencyList && currencyList.find((i) => i.name === GlobalStore.currencyCode)) ?? DEFAULT_CURRENCY,
    amount: '',
    country: (countryList && countryList.find((i) => i.code === GlobalStore.countryCode)) ?? DEFAULT_COUNTRY,
  });

  const getPaymentType = (coinId, subPaymentType) => {
    let result;
    if (coinId === -1) {
      result = PAYMENT_TYPE.GIFTCARD;
    } else {
      result = subPaymentType.fid === -1 ? '' : subPaymentType.fpayid;
    }
    return result;
  };

  const getSubPaymentType = (coinId, subPaymentType, giftCardType) => {
    let result;
    if (coinId === -1) {
      result = giftCardType.fid === -1 ? '' : giftCardType.fid;
    } else {
      result = subPaymentType.fid === -1 ? '' : subPaymentType.fid;
    }
    return result;
  };

  const getCoinId = (coinName) => {
    let coin = coinList.find(item => {
      return item.coinName.toLowerCase() === coinName.toLowerCase();
    });

    if(coin){
      return coin.fcoinid;
    }

    return -1;
  };

  const _getAdvertList = useCallback((
      data = {
        type: -1, 
        currentPage: 1,
        coinId: -1,
        meta: { onSuccess: (res) => {}, onError: () => {} },
    }) => {
      dispatch(
        getAdvertList({
          ...data,
          coinId: data.coinId,
          type,
          subPaymentType: getSubPaymentType(data.coinId, filter.subPaymentType, filter.giftCardType),
          paymentType: getPaymentType(data.coinId, filter.subPaymentType),
          countryId: filter.country.id !== -1 ? filter.country.id : '',
          currencyId: filter.currency.id !== -1 ? filter.currency.id : '',
          amount: filter.amount ?? '',
          meta: {
            onSuccess: async (res) => {
              if (filter.currency.name && filter.country.code) {
                await LocalStorage.setUserGeo({
                  currencyCode: filter.currency.name,
                  countryCode: filter.country.code,
                });
              }
              
              data?.meta?.onSuccess?.();
            },
            onError: () => {
              data?.meta?.onError?.();
            },
          },
        })
      );
    },
    [type, filter]
  );

  const handleChangeFilter = (value) => {
    setFilter({
      ...filter,
      ...value
    });
  }

  useEffect(() => {
    console.log('fresh page');
    _getAdvertList({ 
      ...filter,
      coinId: getCoinId(id),
      type,
    });
  }, [filter, coinList]);
  
  return (
    <div className="fiat-page">
      <div className="container d-flex">
        <FiatSidebar onChange={handleChangeFilter}/>
        <FiatContent data={advertList} currentFilter={filter} onChange={handleChangeFilter} type={type}/>
      </div>
    </div>
  );
}

export default Fiat;
