import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import logo from 'statics/images/logo.png';
import { InputGroup } from 'components/input';
import { SvgIcon } from 'components/icons';
import { EmailIcon, SortRight, TimeIcon, TimesCircle } from 'components/icons/svgs';
import { Button } from 'components/button';
import { Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import * as Yup from 'yup';
import { isArray, isString } from 'utils/validator';
import { sendRegistrationCode } from 'services/api';

RegisterSendCode.propTypes = {
    onChange: PropTypes.func
};

RegisterSendCode.defaultProps = {
    onChange(){}
};

function RegisterSendCode(props) {
    const  { onChange } = props;
    const [email, setEmail] = useState('');
    const [errors, setErrors] = useState(null);
    const [loading, setLoading] = useState(false);
    
    const handleChangeInfo = (value) => {
		setEmail(value);
	}

    let validateSchema = Yup.object().shape({
        email: Yup.string()
            .email('Email không đúng định dạng')
            .required('Vui lòng nhập email')
    });

    const handleSubmit = async () => {
        setLoading(true);
        
        await validateSchema.validate({ email }).then(async () => {
            await sendRegistrationCode(email);
            onChange({email});
        }).catch(function (err) {
            setErrors(err.errors);
        });
       
        setLoading(false);
    }

    return (
        <div className="auth-container animate__animated animate__slideInRight animate__faster">
            <div className="d-flex text-center">
                <div className="logo">
                    <img src={logo} alt="" />
                </div>
            </div>
            <div className="title text-center">
                Sign up
            </div>
            <div className="explain text-center mb-5">
                Register an account with MegaO for secured and speedy digital assets trading
            </div>
            
            {
                errors && <div className="errors">
                {
                    (isString(errors) && errors) && <><SvgIcon icon={<TimesCircle />}></SvgIcon> <p>{errors}</p></>
                }
                {
                    (isArray(errors) && errors.length > 0) && <>
                        <SvgIcon icon={<TimesCircle />}></SvgIcon> { errors.map((error, key) => {
                            return <p key={key} className='err'>{error}</p>
                        }) }
                    </>
                }
                </div>
            }
            
            <div className="form">
                <div className="form-group mb-4">
                    <label>Your Email</label>
                    <InputGroup 
                        prepend={<SvgIcon icon={<EmailIcon />} />} 
                        height={40} 
                        placeholder="example@email.com"
                        onChange={(value) => handleChangeInfo(value)}
                        />
                </div>

                <div className="form-group">
                    <Button 
                        disabled={loading}
                        type="primary" 
                        classList="position-relative" 
                        style={{width: "100%"}}
                        onClick={() => handleSubmit()}>
                        Send code to Email <SvgIcon icon={<SortRight />} size={9} style={{position: 'absolute', right: '8px'}} color="#fff"></SvgIcon>
                        </Button>
                </div>

                <div className="register-footer d-flex align-items-center mt-3">
                    Already have account? <Link to="/login" className='d-flex align-items-center mx-3'><SvgIcon icon={<SortRight />} size={6} color="#076BFF" style={{marginRight: '4px'}}></SvgIcon> Sign In</Link>
                </div>
            </div>
        </div>
    );
}

export default RegisterSendCode;