import { isObject } from './validator';

/**
 * Hàm xoá bỏ *char
 * Tham số mode:
 *     'all': xoá toàn bộ dấu *char
 *     'begin': xoá ký tự *char đầu tiên
 *     'begin_all': xoá tất cả ký tự *char đầu tiên
 *     'end': xoá ký tự *char cuối cùng
 *     'end_all': xoá tất cả ký tự *char cuối cùng
 *     'begin_end': xoá ký tự *char đầu và cuối
 *     'begin_end_all': xoá toàn bộ ký tự *char đầu và cuối
 */

export function removeChar(str, char, mode = 'all') {
	try {
		let pattern;

		if (mode === 'all') {
			pattern = new RegExp(char, 'gm');
		} else if (mode === 'begin') {
			pattern = new RegExp(`^${char}`, 'gm');
		} else if (mode === 'begin_all') {
			pattern = new RegExp(`^${char}*`, 'gm');
		} else if (mode === 'end') {
			pattern = new RegExp(`${char}$`, 'gm');
		} else if (mode === 'end_all') {
			pattern = new RegExp(`${char}*$`, 'gm');
		} else if (mode === 'begin_end') {
			pattern = new RegExp(`(^${char}|${char}$)`, 'gm');
		} else if (mode === 'begin_end_all') {
			pattern = new RegExp(`(^${char}*|${char}*$)`, 'gm');
		}

		if (pattern) {
			return str.replace(pattern, '');
		}

		return str;
	} catch (e) {
		return str;
	}
}

// Hàm replace params
export function replaceParams(str, o) {
	try {
		return str.replace(/{{2}([^{}]*)}{2}/gm, (a, b) => {
			try {
				const r = new Function(`return ${o[b]}`)();
				return typeof r === 'string' || typeof r === 'number' ? r : a;
			} catch (e) {
				return '';
			}
		});
	} catch (e) {
		return str;
	}
}

// Hàm repace options
export function replaceOptions(o1, o2) {
	const rs = {};
	const load = (value) => {
		if (isObject(value)) {
			return Object.keys(value)
				.forEach(key => rs[key] = load(value[key]));
		} else {
			return replaceParams(value, o2);
		}
	};

	load(o1);

	return rs;
}

// Hàm xóa ký tự unicode
export function removeUnicode(s) {
	const uniChars = 'àáảãạâầấẩẫậăằắẳẵặèéẻẽẹêềếểễệđìíỉĩịòóỏõọôồốổỗộơờớởỡợùúủũụưừứửữựỳýỷỹỵÀÁẢÃẠÂẦẤẨẪẬĂẰẮẲẴẶÈÉẺẼẸÊỀẾỂỄỆĐÌÍỈĨỊÒÓỎÕỌÔỒỐỔỖỘƠỜỚỞỠỢÙÚỦŨỤƯỪỨỬỮỰỲÝỶỸỴÂĂĐÔƠƯ';
	const KoDauChars = 'aaaaaaaaaaaaaaaaaeeeeeeeeeeediiiiiooooooooooooooooouuuuuuuuuuuyyyyyAAAAAAAAAAAAAAAAAEEEEEEEEEEEDIIIOOOOOOOOOOOOOOOOOOOUUUUUUUUUUUYYYYYAADOOU';
	let retVal = '';

	for (let i = 0; i < s.length; i++) {
		const pos = uniChars.indexOf(s.charAt(i));

		if (pos >= 0) {
			retVal += KoDauChars.charAt(pos);
		} else {
			retVal += s.charAt(i);
		}
	}

	return retVal;
}

// Hàm xóa ký tự unicode và ký tự đặc biệt
export function removeUnicodeAndAddSeperator(s) {
	const strChar = 'abcdefghijklmnopqrstuvwxyz0123456789';
	let sReturn = '';
	const str = removeUnicode(s.toLowerCase());

	for (let i = 0; i < str.length; i++) {
		if (strChar.indexOf(str.charAt(i)) > -1) {
			if (str.charAt(i) !== ' ') {
				sReturn += str.charAt(i);
			}
		} else if (i > 0 && str.charAt(i - 1) !== ' ' && str.charAt(i - 1) !== '-') {
			if (str.charAt(i).trim() === '-' || str.charAt(i) === ' ') sReturn += '-';
		}
	}

	return sReturn;
}

export function hashCode(str) {
	let hash = 0;

	if (Array.prototype.reduce) {
		hash = str.split('').reduce(function (a, b) {
			let na = a;
			na = ((na << 5) - na) + b.charCodeAt(0);
			return na & na;
		}, 0);
	} else {
		if (str.length === 0) return hash;
		for (let i = 0; i < str.length; i++) {
			const character = str.charCodeAt(i);
			hash = ((hash << 5) - hash) + character;
			hash = hash & hash;
		}
	}

	if (hash < 0) {
		hash *= -1;
	}

	return hash;
}

export function stringFormat(string) {
	const nargs = /\{([0-9a-zA-Z_]+)\}/g;
	let args;

	if (arguments.length === 2 && typeof arguments[1] === 'object') {
		args = arguments[1];
	} else {
		args = new Array(arguments.length - 1);
		for (let i = 1; i < arguments.length; ++i) {
			args[i - 1] = arguments[i];
		}
	}

	if (!args || !args.hasOwnProperty) {
		args = {};
	}

	return string.replace(nargs, function replaceArg(match, i, index) {
		let result;

		if (string[index - 1] === '{' &&
			string[index + match.length] === '}') {
			return i;
		} else {
			result = args.hasOwnProperty(i) ? args[i] : null;

			if (result === null || result === undefined) {
				return '';
			}

			return result;
		}
	});
}

export function stringFormatPaymentMethod(str){
	if(str){
		return str.replace('_', ' ');
	}

	return str;
}