const getAdvertList = function* (
  payload = {
    status: '',
    currentPage: 1,
    meta: { onSuccess: () => {} },
  }
) {
  try {
    const { meta, ...data } = payload;
    const res = yield call(apis.getUserAdvertList, data);
    if (res && res.items) {
      const { items, totalPages } = res;
      const info = { currentPage: data.currentPage, lastPage: totalPages };
      if (items.length > 0) {
        yield put(actions.getAdvertListSuccess({ ...info, items }));
      } else {
        yield put(actions.getAdvertListSuccess(info));
      }
      if (meta && meta.onSuccess) {
        meta.onSuccess();
      }
    }
  } catch (error) {
    yield put(actions.getAdvertListError(error));
  }
};

