const getAdvertList = function*(
  {payload} = {
    coinId: null,
    type: 0,
    currentPage: 1, //optional
    amount: 0, //optional
    currencyId: 0, //optional
    countryId: 0,
    paymentType: 0, //optional
    subPaymentType: 0, //optional
    meta: {
      onSuccess: () => {},
      onError: () => {}
    }
  }
) {
  const { meta, ...data } = payload;

  try {
    if (!data.currentPage) {
      data.currentPage = 1;
    }

    const res = yield call(apis.getAdvertList, data);

    if (res && res.items) {
      const { items, totalPages, isFallback } = res;
      const info = {
        currentPage: data.currentPage,
        lastPage: totalPages,
        isFallback: isFallback || false
      };
      
      if (items.length > 0) {
        yield put(getAdvertListSuccess({ ...info, items }));
      } else {
        yield put(getAdvertListSuccess(info));
      }
      if (meta && meta.onSuccess) {
        meta.onSuccess(res);
      }
    }
  } catch (error) {
    yield put(getAdvertListError(error));
    
    if (meta && meta.onError) {
      meta.onError();
    }
  }
};

