import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled from '@emotion/styled';
import { isNumber } from 'utils/validator';

import './style.scss';

class Checkbox extends Component {
	static propTypes = {
		disabled: PropTypes.bool,
		label: PropTypes.any,
		value: PropTypes.any,
		checked: PropTypes.bool,
		boxSize: PropTypes.oneOfType([
			PropTypes.number,
			PropTypes.string
		]),
		borderRadius: PropTypes.oneOfType([
			PropTypes.number,
			PropTypes.string
		]),
		buttonColor: PropTypes.string,
		backgroundColor: PropTypes.string,
		backgroundColorChecked: PropTypes.string,
		labelSize: PropTypes.oneOfType([
			PropTypes.number,
			PropTypes.string
		]),
		labelColor: PropTypes.string,
		labelBold: PropTypes.bool,
		onChange: PropTypes.func,
		readOnly: PropTypes.bool,
		style: PropTypes.object,
	}

	static defaultProps = {
		disabled: false,
		label: null,
		value: null,
		checked: false,
		boxSize: 20,
		borderRadius: 4,
		buttonColor: '#fff',
		backgroundColor: '#fff',
		backgroundColorChecked: '#003399',
		labelSize: 15,
		labelColor: '#333',
		labelBold: false,
		onChange() {},
		readOnly: false,
		style: {}
	}

	constructor(props) {
		super(props);

		this.state = {
			checked: props.checked
		};

		this.handleChange = this.handleChange.bind(this);
	}

	componentWillReceiveProps(nextProps) {
		const { checked } = this.state;

		if (nextProps.checked !== checked) {
			this.setState({
				checked: nextProps.checked
			});
		}
	}

	handleChange(e) {
		const { readOnly, value, disabled, onChange } = this.props;

		if (readOnly !== true && disabled !== true) {
			const checked = e.target.checked;

			this.setState({
				checked
			}, () => {
				onChange(checked, value);
			});
		}
	}

	render() {
		const { checked } = this.state;
		const {
			label,
			labelSize,
			labelColor,
			boxSize,
			buttonColor,
			disabled,
			labelBold,
			backgroundColor,
			backgroundColorChecked,
			style,
			borderRadius
		} = this.props;

		const styleLabel = {
			fontSize: labelSize,
			color: labelColor,
			fontWeight: labelBold ? 'bold': null
		};

		let styleInput = {
			backgroundColor,
			borderColor: '#ddd',
			width: boxSize,
			height: boxSize,
			borderRadius
		};

		let styleInputAfter = {
			borderLeft: `3px solid ${buttonColor}`,
			borderBottom: `3px solid ${buttonColor}`,
			width: `calc(${isNumber(boxSize+3) ? `${boxSize+3}px` : boxSize+3} / 2.5)`,
			height: `calc(${isNumber(boxSize+3) ? `${boxSize+3}px` : boxSize+3} / 5)`,
			left: `calc(${isNumber(boxSize+3) ? `${boxSize+3}px` : boxSize+3} / 5.5)`,
			top: `calc(${isNumber(boxSize+3) ? `${boxSize+3}px` : boxSize+3} / 4.5)`
		};

		if (checked === true) {
			styleInput = {
				...styleInput,
				backgroundColor: backgroundColorChecked,
				borderColor: backgroundColorChecked,
				'&::after': styleInputAfter
			};
		}

		if (disabled === true) {
			styleInputAfter = {
				...styleInputAfter,
				borderColor: '#ddd'
			};

			styleInput = {
				...styleInput,
				backgroundColor: '#f0f0f0',
				borderColor: '#ddd',
				'&::after': styleInputAfter
			};
		}

		const CheckboxInput = styled.div(styleInput);

		return (
			<div className="ui-checkbox" style={style}>
				<label className="checkbox-container">
					<input type="checkbox"
						checked={checked}
						onChange={this.handleChange}
					/>

					<CheckboxInput className="checkbox-input" />

					{
						label ?
							<span className="checkbox-label" style={styleLabel}>
								{label}
							</span>
							: null
					}
				</label>
			</div>
		);
	}
}

export default Checkbox;