import 'moment/locale/vi';

import { MessageDescriptor, setupI18n } from '@lingui/core';
import moment from 'moment';

import catalogEn from 'locales/en/messages.js';
import catalogVi from 'locales/vi/messages.js';

const catalogs = { vi: catalogVi, en: catalogEn };

let i18n = null;

export const setUpI18n = (language = 'en') => {
  if (language === 'vi') {
    moment.locale('vi');
  } else {
    moment.locale('en');
  }

  i18n = setupI18n({ language, catalogs: catalogs });
  return i18n;
};

export const trans = (message) => {
  // console.log(i18n);
  // return (i18n ? i18n._(message) : message.id);
  return (i18n ? i18n._(message) : message);
}
