import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';

import "./style.scss";
import logo from 'statics/images/logo.png';
import {Button, ButtonGroup} from 'components/button';
import FontAwesome from 'components/icons/FontAwesome';
import SvgIcon from 'components/icons/SvgIcon';
import {EarthIcon, AppIcon, SortDown, VietnamIcon, EnglishIcon, GoogleStoreIcon, AppStoreIcon, UserIcon} from 'components/icons/svgs';
import { Link } from 'react-router-dom';
import { BuySellButton, UserDropdown, NotificationDropdown } from './components';
import {Dropdown, DropdownItem, DropdownMenu, DropdownToggle} from 'components/dropdown';
import Dropdown2 from 'components/dropdown2';
import SelectBox from 'components/selectbox';
import GlobalStore from 'constants/global';
import {selectAuth} from 'redux/authSlide';
import { useSelector } from 'react-redux';
import { selectUserInfo, selectUserBalances, selectUserIsLoading } from 'redux/userSlide';

Header.propTypes = {
    
};

function Header(props) {
    const [isLogin, setIsLogin] = useState(false);
    const userInfo = useSelector(selectUserInfo);
    const userBalances = useSelector(selectUserBalances);
    const userIsLoading = useSelector(selectUserIsLoading);

    const handleChangeLang = (value) => {
        console.log(value);
    }

    const handleChangeStore = (value) => {
        window.open(value.value, '_blank');

    }

    return (
        <header className='position-fixed w-100'>
            <div className="d-block">
                <div id='top-menu-navbar' className="container-fluid">
                    <div className="container navbar-container">
                        <div className="d-flex align-items-center">
                            <button className='hamburger-menu d-xl-none'></button>

                            <Link className="top-menu-button-home" to="/"><img src={logo} alt="" /></Link>

                            <nav className='navbar-left'>
                                <ul className='p-0 m-0 list-unstyled d-flex align-items-center'>
                                    <li>
                                        <BuySellButton type="buy" data={[]}/>
                                    </li>
                                    <li>
                                        <BuySellButton type="sell" data={[]}/>
                                    </li>
                                    <Link reloadDocument={true} to="/buy/btc" className='font-weight-bold'>Fiat</Link>
                                </ul>
                            </nav>

                            <div className="navbar-right">
                                <ul className='p-0 m-0 list-unstyled d-flex align-items-center'>
                                    {
                                        (!GlobalStore.isLoginUser) && <>
                                            <li className='li-login'>
                                                <Link to="/login"><Button type="default" outline={true} bold={true} height={31}>Login</Button></Link>
                                            </li>
                                            <li className='li-register'>
                                                <Link to="/register"><Button type="primary" bold={true} height={31}>Sign Up</Button></Link>
                                            </li>
                                        </>
                                    }

                                    {
                                        (GlobalStore.isLoginUser) && <>
                                            <li className='me-4'>
                                                <Link to="/login"><strong>My offers</strong></Link>
                                            </li>
                                            <li className='me-2'>
                                                <div>
                                                    <div className='nickname'>{userInfo && userInfo.user.fnickname}</div>
                                                    <div className='balance'><span style={{color: '#0ECB81'}}>{userBalances ? userBalances.fiatAssets : 0} {userBalances ? userBalances.currencyName : ''}</span> <FontAwesome icon="fas fa-eye" /></div>
                                                </div>
                                            </li>

                                            <li className='me-2'>
                                                { (userIsLoading === false) ? <UserDropdown /> : ''}
                                            </li>

                                            <li className='me-3'>
                                                <NotificationDropdown />
                                            </li>
                                        </>
                                    }
                                    
                                    <li className='li-app'>
                                        <Dropdown2
                                            key='dropdonw-download'
                                            classList='ui-dropdown--store'
                                            style={{
                                                // margin: "0px 10px",
                                                height: "100%",
                                                display: "flex",
                                                alignItems: "center"
                                            }}
                                            toggle={<Button type="default" height={31}><SvgIcon icon={<AppIcon />} color="#333"/> APP</Button>}
                                            data={
                                                [
                                                    {
                                                        label: "Xóa",
                                                        color: "#333",
                                                        type: 'storeUrl',
                                                        value: 'https://play.google.com/store/apps/details?id=io.megao&hl=en&gl=US',
                                                        elm: <SvgIcon icon={<GoogleStoreIcon />} style={{width: '100%'}} />
                                                    },
                                                    {
                                                        label: "Xóa",
                                                        color: "#333",
                                                        type: 'storeUrl',
                                                        value: 'https://apps.apple.com/us/app/megao/id1599068719',
                                                        elm: <SvgIcon icon={<AppStoreIcon />} style={{width: '100%'}} />
                                                    }
                                                ]
                                            }
                                            onChange={(value) => handleChangeStore(value)}
                                        />
                                    </li>
                                    <li className='li-language'>
                                        <Dropdown2
                                            key='dropdonw-language'
                                            classList='ui-dropdown--lang'
                                            style={{
                                                height: "100%",
                                                display: "flex",
                                                alignItems: "center"
                                            }}
                                            toggle={<Button type="default" height={31}><SvgIcon icon={<EarthIcon />} color="#333" className="earth-icon"/> <span>EN</span> <SvgIcon icon={<SortDown />} size={16} color="#333"/></Button>}
                                            data={
                                                [
                                                    {
                                                        label: "Xóa",
                                                        color: "#333",
                                                        type: 'lang',
                                                        value: 'vi',
                                                        elm: <span>Việt Nam <SvgIcon icon={<VietnamIcon />} /></span>
                                                    },
                                                    {
                                                        label: "Xóa",
                                                        color: "#333",
                                                        type: 'lang',
                                                        value: 'en',
                                                        elm: <span>English <SvgIcon icon={<EnglishIcon />} /></span>
                                                    }
                                                ]
                                            }
                                            onChange={(value) => handleChangeLang(value)}
                                        />
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </header>
    );
}

export default Header;