import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { isUndefined } from 'utils/validator';

import './style.scss';

import Input from './Input';
import clsx from 'clsx';

class InputGroup extends Component {
	static propTypes = {
		prepend: PropTypes.any,
		preBgColor: PropTypes.string,
		preBorder: PropTypes.bool,
		preColor: PropTypes.string,
		append: PropTypes.any,
		apBgColor: PropTypes.string,
		apBorder: PropTypes.bool,
		apColor: PropTypes.string,
		apStyle: PropTypes.object,
		preStyle: PropTypes.object,
		classList: PropTypes.string,
		value: PropTypes.oneOfType([
			PropTypes.string,
			PropTypes.number
		]),
		min: PropTypes.number,
		max: PropTypes.number,
		type: PropTypes.oneOf([
			'number', 'text', 'password'
		]),
		height: PropTypes.oneOfType([
			PropTypes.number,
			PropTypes.string
		]),
		width: PropTypes.oneOfType([
			PropTypes.number,
			PropTypes.string
		]),
		borderRadius: PropTypes.oneOfType([
			PropTypes.number,
			PropTypes.string
		]),
		style: PropTypes.object,
		placeholder: PropTypes.string,
		autoComplete: PropTypes.bool,
		onChange: PropTypes.func,
		onKeyDown: PropTypes.func,
		onEnter: PropTypes.func
	}

	static defaultProps = {
		placeholder: '',
		preBgColor: '#eee',
		preBorder: true,
		preColor: '#333',
		apBgColor: '#eee',
		apBorder: true,
		apColor: '#333',
		classList: '',
		value: null,
		type: 'text',
		height: 30,
		width: '100%',
		borderRadius: 3,
		style: {},
		apStyle: {},
		preStyle: {},
		autoComplete: true,
		onChange() {},
		onKeyDown() {},
		onEnter() {}
	}

	constructor(props) {
		super(props);
	}

	render() {
		const {
			prepend,
			preBgColor,
			preBorder,
			preColor,
			append,
			apBgColor,
			apBorder,
			apColor,
			value,
			type,
			height,
			width,
			min,
			max,
			borderRadius,
			style,
			onChange,
			onKeyDown,
			placeholder,
			autoComplete,
			onEnter,
			classList,
			apStyle,
			preStyle
		} = this.props;

		return (
			<div className={clsx('ui-input-group', classList)} style={{
				height,
				width,
				borderRadius,
				...style
			}}>
				{
					!isUndefined(prepend) ?
						<div className="group-prepend" style={{
							// backgroundColor: preBgColor,
							// borderRight: preBorder ? '1px solid #ddd' : '',
							color: preColor,
							...preStyle
						}}>
							{ prepend }
						</div>
						: null
				}

				<Input
					value={value}
					type={type}
					height="100%"
					width="100%"
					min={min}
					max={max}
					placeholder={placeholder}
					autoComplete={autoComplete}
					onChange={onChange}
					onKeyDown={onKeyDown}
					onEnter={onEnter}
				/>

				{
					!isUndefined(append) ?
						<div className="group-append" style={{
							backgroundColor: apBgColor,
							//borderLeft: apBorder ? '1px solid #ddd' : '',
							color: apColor,
							...apStyle
						}}>
							{ append }
						</div>
						: null
				}
			</div>
		);
	}
}

export default InputGroup;