import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './style.scss';

class Button extends Component {
	static displayName = 'Button';
	
	static propTypes = {
		children: PropTypes.any,
		icon: PropTypes.any,
		type: PropTypes.oneOf([
			'default',
			'primary',
			'success',
			'info',
			'warning',
			'danger',
			'white',
			'link'
		]),
		size: PropTypes.oneOf([
			'default',
			'medium',
			'small',
			'mini'
		]),
		disabled: PropTypes.bool,
		readOnly: PropTypes.bool,
		outline: PropTypes.bool,
		bold: PropTypes.bool,
		width: PropTypes.oneOfType([
			PropTypes.string,
			PropTypes.number
		]),
		height: PropTypes.oneOfType([
			PropTypes.string,
			PropTypes.number
		]),
		classList: PropTypes.string,
		borderColor: PropTypes.string,
		bgColor: PropTypes.string,
		textColor: PropTypes.string,
		radius: PropTypes.oneOfType([
			PropTypes.string,
			PropTypes.number
		]),
		float: PropTypes.string,
		style: PropTypes.object,
		onClick: PropTypes.func
	}

	static defaultProps = {
		children: null,
		icon: null,
		type: 'default',
		size: 'default',
		disabled: false,
		readOnly: false,
		outline: false,
		bold: false,
		width: null,
		height: null,
		borderColor: null,
		bgColor: null,
		textColor: null,
		radius: null,
		float: null,
		classList: '',
		style: {},
		onClick() {}
	}

	constructor(props) {
		super(props);

		this.handleClick = this.handleClick.bind(this);
	}

	handleClick(e) {
		const { disabled, readOnly, onClick } = this.props;

		if (!disabled && !readOnly) {
			onClick(e);
		}
	}

	getClasses() {
		const { type, size, disabled, outline, classList } = this.props;
		const classes = [
			`button-${type}`,
			`size-${size}`
		];

		if (disabled) {
			classes.push('is-disabled');
		}

		if (outline) {
			classes.push('is-outline');
		}

		classes.push(classList);

		return classes.join(' ');
	}

	render() {
		const {
			children,
			width,
			height,
			borderColor,
			bgColor,
			textColor,
			radius,
			float,
			bold,
			icon,
			margin,
			style
		} = this.props;

		return (
			<button
				className={`ui-button ${this.getClasses()}`}
				style={{
					width,
					height,
					borderColor,
					backgroundColor: bgColor,
					color: textColor,
					borderRadius: radius,
					fontWeight: bold ? 'bold': 'normal',
					float,
					margin,
					...style
				}}
				onClick={this.handleClick}
			>
				{ icon && <div className="button-icon">{ icon } </div> }
				{ children }
			</button>
		);
	}
}

export default Button;