import React, { useEffect, useState } from 'react';
import { useDispatch, connect } from 'react-redux'
import PropTypes from 'prop-types';
import * as Yup from 'yup';
import './style.scss';
import logo from 'statics/images/logo.png';
import SvgIcon from 'components/icons/SvgIcon';
import {EmailIcon, PasswordIcon, SortRight, TimesCircle} from 'components/icons/svgs';
import { Button } from 'components/button';
import { Link } from 'react-router-dom';
import { Input, InputGroup } from 'components/input';
import { FontAwesome } from 'components/icons';
import Checkbox from 'components/checkbox';
import { Trans, t } from '@lingui/macro';
import { trans } from 'config/LinguiConfig';
import { isArray, isString } from 'utils/validator';
import { MessageBox, renderDialog } from 'components/dialog';
import GlobalStore from 'constants/global';
import { uuid } from 'utils/externals';
import {actions, selectAuth} from 'redux/authSlide';
import { useSelector } from 'react-redux';
import { useNavigate } from "react-router-dom";
import { md5 } from 'utils/externals';

RegisterInfo.propTypes = {
    email: PropTypes.string,
    verifyCode: PropTypes.string,
    onChange: PropTypes.func,
};

RegisterInfo.defaultProps = {
    email: '',
    verifyCode: '',
    onChange(){},
};

function RegisterInfo(props) {
    const { email, verifyCode } = props;
    const [formInfo, setFormInfo] = useState({});
    const [errors, setErrors] = useState([]);
    const dispatch = useDispatch();
    const authState = useSelector(selectAuth);
    const navigate = useNavigate();
    
    useEffect(() => {
        setErrors(authState.error);
    }, [authState.error]);

    const handleChangeInfo = (field = '', value = '') => {
        const newData = {};
        newData[field] = value;

        setFormInfo({
            ...formInfo,
            ...newData
        });
    }

    const validateSchema = Yup.object().shape({
        nickname: Yup.string().required(trans(t`Vui lòng nhập tên hiển thị`)),
        password: Yup.string()
            .required(trans(t`Vui lòng nhập mật khẩu`))
            .matches(
            /^(?![0-9]+$)(?![a-zA-Z]+$)[\S]{6,}$/,
            trans(t`Độ dài mật khẩu phải lớn hơn hoặc bằng 6 và bao gồm cả chữ và số!`)
            ),
        confirmPassword: Yup.string()
            .oneOf([Yup.ref('password'), null], trans(t`Mật khẩu xác nhận không đúng`))
            .required(trans(t`Vui lòng nhập mật khẩu xác nhận`))
    });

    const handleSubmit = async () => {
        await validateSchema.validate(formInfo).catch(function (err) {
            setErrors(err.errors);
            return;
        });

        const { checkbox, password, nickname, inviteCode } = formInfo;
        
        if (!checkbox) {
            renderDialog(
                <MessageBox type="error">{trans(t`Vui lòng đọc và đồng ý với Điều khoản thỏa thuận người dùng`)}</MessageBox>
            );
        } else {
            let did = GlobalStore.uuid;

            if(!did){
                did = uuid();
            }

            const params = {
                nickname,
                email,
                verifyCode,
                inviteCode,
                uuid: did,
                password: md5(password),
            };

            dispatch(actions.register(params));

            navigate('/user/welcome');
        }
    };

    return (
        <div className="auth-container animate__animated animate__slideInRight animate__faster">
            <div className="d-flex text-center">
                <div className="logo">
                    <img src={logo} alt="" />
                </div>
            </div>
            <div className="title text-center">
                Sign up MEGAO
            </div>
            <div className="explain text-center mb-4">
                Please complete the below information to proceed the account registration!
            </div>

            {
                errors && <div className="errors">
                {
                    (isString(errors) && errors) && <><SvgIcon icon={<TimesCircle />}></SvgIcon> <p>{errors}</p></>
                }
                {
                    (isArray(errors) && errors.length > 0) && <>
                        <SvgIcon icon={<TimesCircle />}></SvgIcon> { errors.map((error, key) => {
                            return <p key={key} className='err'>{error}</p>
                        }) }
                    </>
                }
                </div>
            }
            
            <div className="form">
                <div className="form-group mb-4">
                    <label>Username</label>
                    <InputGroup onChange={(value) => handleChangeInfo('nickname', value)} preStyle={{color: 'rgb(153 153 153)'}} prepend={<FontAwesome icon="fas fa-user" style={{padding: '0px 5px'}} />} height={40} placeholder="Username"/>
                </div>
                
                <div className="form-group mb-4">
                    <label>Password</label>
                    <InputGroup onChange={(value) => handleChangeInfo('password', value)} type="password" prepend={<SvgIcon icon={<PasswordIcon />} />} height={40} placeholder="Login password"/>
                </div>

                <div className="form-group mb-4">
                    <label>Confirm password</label>
                    <InputGroup onChange={(value) => handleChangeInfo('confirmPassword', value)} type="password" prepend={<SvgIcon icon={<PasswordIcon />} />} height={40} placeholder="Re-enter login password"/>
                </div>

                <div className="form-group mb-4">
                    <label>Invitation code (optional)</label>
                    <InputGroup onChange={(value) => handleChangeInfo('inviteCode', value)}  type="text" preStyle={{color: 'rgb(153 153 153)'}} prepend={<SvgIcon icon={<FontAwesome icon="far fa-code" style={{padding: '0px 5px', fontSize: '15px'}}/>} />} height={40} placeholder="Invitation code"/>
                </div>

                <div className="form-group mb-4 d-flex align-items-center">
                    <Checkbox 
                        checked={formInfo.checkbox} 
                        onChange={(checked, value) => handleChangeInfo('checkbox', checked)} 
                        label=''></Checkbox><span className='ms-2'>I have read and agreed to the  <Link to="/" className='fw-bolder'>User agreement</Link></span>
                </div>

                <div className="form-group">
                    <Button 
                        type="primary" 
                        classList="position-relative" 
                        style={{width: "100%"}}
                        onClick={() => handleSubmit()}
                        >Register <SvgIcon icon={<SortRight />} size={9} style={{position: 'absolute', right: '8px'}} color="#fff"></SvgIcon>
                        </Button>
                </div>

                <div className="register-footer d-flex align-items-center mt-3">
                    Already have account? <Link to="/login" className='d-flex align-items-center mx-3'><SvgIcon icon={<SortRight />} size={6} color="#076BFF" style={{marginRight: '4px'}}></SvgIcon> Login</Link>
                </div>
            </div>
        </div>
    );
}

export default RegisterInfo;