import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import './style.scss';
import logo from 'statics/images/logo.png';
import SvgIcon from 'components/icons/SvgIcon';
import {EmailIcon, PasswordIcon, SortRight, TimesCircle} from 'components/icons/svgs';
import { Button } from 'components/button';
import { Link } from 'react-router-dom';
import { Input, InputGroup } from 'components/input';
import * as Yup from 'yup';
import { Trans, t } from '@lingui/macro';
import { trans } from 'config/LinguiConfig';
import {selectAuth, actions} from 'redux/authSlide';
import { useDispatch, useSelector } from 'react-redux';
import { isArray, isString } from 'utils/validator';
import { MessageBox, ModalBox } from 'components/dialog';

ForgotPassword.propTypes = {
    
};

function ForgotPassword(props) {
    const [errors, setErrors] = useState([]);
    const [formInfo, setFormInfo] = useState({});
    const [showSuccess, setShowSuccess] = useState(false);
    const authState = useSelector(selectAuth);

    const dispatch  = useDispatch();

    useEffect(() => {
        setErrors(authState.error);
    }, [authState.error]);

    const schema = Yup.object().shape({
        email: Yup.string()
            .email(trans(t`Email không đúng định dạng`))
            .required(trans(t`Vui lòng nhập email`))
    });

    const handleChangeInfo = (field = '', value = '') => {
        const newData = {};
        newData[field] = value;

        setFormInfo({
            ...formInfo,
            ...newData
        });
    }

    const handleSubmit = async() => {
        await schema.validate(formInfo).then(() => {
            dispatch(actions.sendResetPasswordCode(formInfo));
            setShowSuccess(true);
        }).catch(function (err) {
            setErrors(err.errors);
            return;
        });
    }
    
    const renderSuccess = () => {
        return <MessageBox type="success" title={trans(t`Successfully!`)} visible={showSuccess} onClose={() => {
            setShowSuccess(false);
        }}>
            {trans(t`Tin nhắn được gửi thành công, vui lòng kiểm tra email và nhấp vào liên kết`)}
        </MessageBox>
    }

    return (
        <div className='auth-page'>
            <div className="container-fluid">
                <div className="auth-form">
                    <div className="auth-container animate__animated animate__slideInRight animate__faster">
                        <div className="d-flex text-center">
                            <div className="logo">
                                <img src={logo} alt="" />
                            </div>
                        </div>
                        <div className="title text-center">
                            Quên mật khẩu
                        </div>
                        <div className="explain text-center mb-5">
                            Để lấy lại mật khẩu, vui lòng nhập email đăng ký của bạn!
                        </div>

                        {
                            errors && <div className="errors">
                            <SvgIcon icon={<TimesCircle />}></SvgIcon>
                            {
                                isString(errors) && <p>{errors}</p>
                            }
                            {
                                isArray(errors) && errors.map((error, key) => {
                                    return <p key={key} className='err'>{error}</p>
                                })
                            }
                            </div>
                        }
                        
                        <div className="form">
                            <div className="form-group mb-4">
                                <label>Your Email</label>
                                <InputGroup onChange={(value) => handleChangeInfo('email', value)} prepend={<SvgIcon icon={<EmailIcon />} />} height={40} placeholder="example@email.com"/>
                            </div>
                            <div className="form-group">
                                <Button 
                                    type="primary" 
                                    classList="position-relative" 
                                    style={{width: "100%"}}
                                    onClick={() => handleSubmit()}
                                    >Send email <SvgIcon icon={<SortRight />} size={9} style={{position: 'absolute', right: '8px'}} color="#fff"></SvgIcon>
                                    </Button>
                            </div>
                            <div className="form-group">
                                <div className="register-footer d-flex align-items-center mt-3">
                                    <Link to="/login" className='d-flex align-items-center me-4'><SvgIcon icon={<SortRight />} size={6} color="#076BFF" style={{marginRight: '4px'}}></SvgIcon> Login</Link>
                                </div>
                            </div>
                            <div className="form-group">
                                <div className="register-footer d-flex align-items-center mt-1">
                                    <Link to="/register" className='d-flex align-items-center'><SvgIcon icon={<SortRight />} size={6} color="#076BFF" style={{marginRight: '4px'}}></SvgIcon> Sign up</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                    {renderSuccess()}
                </div>
            </div>
        </div>
    );
}

export default ForgotPassword;