import { Component, cloneElement, isValidElement } from 'react';
import PropTypes from 'prop-types';

import './svg-icon.scss';

class SvgIcon extends Component {
	static propTypes = {
		style: PropTypes.object,
		size: PropTypes.number,
		color: PropTypes.string,
		hoverColor: PropTypes.string,
		icon: PropTypes.element,
		onClick: PropTypes.func
	};
	
	static defaultProps = {
		style: {},
		size: 24,
		color: '#666',
		hoverColor: null,
		icon: null,
		onClick() {}
	};

	constructor(props) {
		super(props);

		this.state = {
			activeColor: props.color
		};
	}

	static componentWillReceiveProps(nextProps) {
		const { color } = this.props;

		if (color !== nextProps.color) {
			this.setState({
				activeColor: nextProps.color
			});
		}
	}

	render() {
		const { style, size, color, hoverColor, icon, onClick } = this.props;
		const { activeColor } = this.state;

		return (
			<div className="ui-svg-icon"
				style={{
					...style,
					width: size,
					height: size
				}}
				onMouseOver={() => this.setState({ activeColor: hoverColor || color })}
				onMouseLeave={() => this.setState({ activeColor: color })}
				onClick={onClick}
			>
				{
					isValidElement(icon) && cloneElement(icon, {
						color: activeColor,
						size
					})
				}
			</div>
		);
	}
}

export default SvgIcon;