import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import { Input, InputGroup } from 'components/input';
import { Dropdown, DropdownItem, DropdownMenu, DropdownToggle } from 'components/dropdown';
import { FontAwesome } from 'components/icons';
import LocalStorage from 'config/localStorage';
import './style.scss';

CurrencySelectField.propTypes = {
  onChange: PropTypes.func,
};

CurrencySelectField.defaultProps = {
  onChange: () => {},
};

function CurrencySelectField(props) {
  const [toggle, setToggle] = useState(false);
  const [data, setData] = useState([]);
  const ref = useRef();
  const refDropDownmnu = useRef();

  useEffect(() => {
    async function fetchData() {
      const currencyList = await LocalStorage.getDataList('currencyList');

      setData(currencyList);
    }
    fetchData();
  }, []);

  useEffect(() => {
    const checkIfClickedOutside = (e) => {
      if (toggle && ref.current && !ref.current.contains(e.target) && !refDropDownmnu.current.contains(e.target)) {
        setToggle(false);
      }
    };

    document.addEventListener('mousedown', checkIfClickedOutside);

    return () => {
      document.removeEventListener('mousedown', checkIfClickedOutside);
    };
  }, [toggle]);

  const handleToggle = (toggle) => {
    setToggle(!toggle);
    setData(data);
  };

  const handleSelectCurrency = (value) => {
    setToggle(false);
    // setData(data);
  };

  const handleSearch = (value) => {
    value = value.toLocaleLowerCase().trim();

    const newData = data.filter(item => {
      return (item.nameStr).toLocaleLowerCase().search(value) > -1;
    });

    setData(newData);
  }

  return (
    <div
      className="position-relative d-flex ui-shadow w-100 justify-content-between align-items-center px-2"
      style={{ background: '#fff', height: '46px', fontWeight: 'bold', borderRadius: '5px', padding: 'auto 8px' }}
    >
      <Input type="text" placeholder="Any Amount" style={{ border: '0px', width: '75%' }}></Input>

      <Dropdown type="group" classList="position-static">
        <div ref={ref}>
          <DropdownToggle status={toggle} onToggle={handleToggle} height={29}>
            <span className="">VND</span>
          </DropdownToggle>
        </div>

        <div id="dropdownmenu-wrap" ref={refDropDownmnu} style={{maxHeight: '500px', overflowY: 'auto'}}>
          <DropdownMenu open={toggle} style={{ width: '100%', left: '0px' }} classList="ui-shadow ui-dropdown--currency mt-1 dropdown-menu">
            <InputGroup
              prepend={<FontAwesome icon="fal fa-search" />}
              preStyle={{paddingRight: 0}}
              height={40}
              placeholder="Find payment method"
              classList="mb-2"
              onChange={(value) => handleSearch(value)}
            ></InputGroup>

            <div className="pt-1 ml-2 text-uppercase text-gray" style={{fontSize: '12px'}}>Most popular</div>

            {data ? data.map((item, idx) => {
              return (
                <DropdownItem key={`currency-${idx}`}>
                  <div className="offer-dropdown__item d-block" onClick={() => handleSelectCurrency(item.id)}>
                    <span className="d-flex flex-grow-1 align-items-center">
                      {/* <SvgIcon icon={<BitcoinIcon />} size={16} /> */}
                      <span className="d-flex flex-grow-1 pr-2 pl-3 ml-3 flex-column">{item.fullName}</span><span>{item.name}</span>
                    </span>
                  </div>
                </DropdownItem>
              );
            }) : ''}
          </DropdownMenu>
        </div>
      </Dropdown>
    </div>
  );
}

export default CurrencySelectField;
