import { getExtname } from './path';

export function isDefined(val) {
	return typeof val !== 'undefined';
}

export function isUndefined(val) {
	return typeof val === 'undefined';
}

export function isObject(val) {
	return {}.toString.call(val) === '[object Object]';
}

export function isFunction(val) {
	return typeof val === 'function';
}

export function isString(val) {
	return typeof val === 'string';
}

export function isArray(val) {
	return {}.toString.call(val) === '[object Array]';
}

export function isNumber(val) {
	return typeof val === 'number' && !isNaN(val);
}

export function isBoolean(val) {
	return typeof val === 'boolean';
}

export function isNull(val) {
	return {}.toString.call(val) === '[object Null]';
}

export function isEmpty(val) {
	const EMPTY_STRING_REGEXP = /^\s*$/;

	if (isNull(val)) return true;
	if (isString(val)) return EMPTY_STRING_REGEXP.test(val);
	if (isFunction(val)) return false;
	if (isArray(val)) return val.length === 0;
	if (isObject(val)) return Object.keys(val).length === 0;

	return false;
}

export function isRegExp(val) {
	return {}.toString.call(val) === '[object RegExp]';
}

export function isUrl(val) {
	const URL_REGEXP = /^(https?:\/\/|:?\/\/|www.)[0-9a-zA-Z.-]*[.][a-zA-Z]{2,}/i;

	return URL_REGEXP.test(val);
}

export function isPromise(val) {
	return {}.toString.call(val) === '[object Promise]';
}

export function isClass(val) {
	return isFunction(val) && /^class\s/.test(Function.prototype.toString.call(val));
}

export function isImageBase64(val) {
	return /^data:image\/.*;/.test(val);
}

export function isLinkCrop(val) {
	const LINK_CROP_REGEXP = /^(https?:\/\/|:?\/\/|www.)?[0-9a-zA-Z.-]*[.][a-zA-Z]{2,}\/crop\/[0-9]*,[0-9]*_[0-9]*,[0-9]*\//;

	return LINK_CROP_REGEXP.test(val);
}

export function isValidFile(filename, acceptTypes) {
	const ext = getExtname(filename);
	const arrAccept = acceptTypes.split(',');

	if (arrAccept.lastIndexOf(ext) !== -1 && ext) {
		return true;
	} else {
		return false;
	}
}

export function isDate(val) {
	return {}.toString.call(val) === '[object Date]';
}

export function isError(val) {
	return {}.toString.call(val) === '[object Error]';
}

export function isHtml(str) {
	if (isString(str)) {
		const regex = /<\/?[a-z0-9]+>/igm;
		return regex.test(str);
	} else {
		return false;
	}
}

export function isVNode(node) {
    if(isObject(node) && node.hasOwnProperty('isAsyncPlaceholder')) {
        return true;
    }

    return false;
}
