import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import clsx from 'clsx';

Dropdown.propTypes = {
    classList: PropTypes.string,
    stlye: PropTypes.object,
    type: PropTypes.oneOf(['normal', 'group']),

};

Dropdown.defaultProps = {
    classList: '',
    stlye: {},
    type: 'normal'
}

function Dropdown(props) {
    const {stlye, classList, type, children } = props;

    
    return (
        <div className={clsx('ui-dropdown', classList)}  style={stlye}>
            {/* <div className={clsx('ui-dropdown', {"ui-button-group position-relative": type === 'group'})}> */}
                {children}
            {/* </div> */}
        </div>
    );
}

export default Dropdown;