import React from 'react';
import PropTypes from 'prop-types';
import './style.scss';
import clsx from 'clsx';

Switch2.propTypes = {
    classList: PropTypes.string,
    style: PropTypes.object,
    data: PropTypes.array,
    value: PropTypes.any,
    onChange: PropTypes.func,
};

Switch2.defaultProps = {
    classList: '',
    style: {},
    data: [],
    value: '',
    onChange() {},
};

function Switch2(props) {
    const { onChange, classList, style, data, value } = props;

    const handleClick = (value) => {
        onChange(value);
    }

    return (
        <div className={clsx('ui-switch2', classList)} style={style}>
            {
                data.map((item, idx) => {
                    return item.value === value ? <span key={idx} className='ui-switch2-active' onClick={() => handleClick(item.value)}>{item.label}</span> : <span key={idx} onClick={() => handleClick(item.value)}>{item.label}</span>        
                })
            }
        </div>
    );
}

export default Switch2;