import { isArray, isError, isFunction } from './validator';
import { getFilename } from './path';

// Hàm trả về instance của Error
export function error(msg, code) {
  let err;

  if (isError(msg)) {
    err = new Error(msg.message);
    err.code = msg.code || code;
  } else {
    err = new Error(msg);
    err.code = code;
  }

  return err;
}

// Hàm clone một Date, RegExp, Array, Object
export function clone(o) {
  if (o === null) {
    return o;
  }

  const t = typeof o;

  if (t === 'string' || t === 'number' || t === 'undefined' || t === 'function') {
    return o;
  }

  if (o instanceof Date) {
    return new Date(o.getTime());
  }

  if (o instanceof RegExp) {
    return new RegExp(o);
  }

  if (Array.isArray(o)) {
    return o.map((item) => clone(item));
  }

  if (o instanceof Object) {
    const no = {};

    for (const k in o) {
      no[k] = clone(o[k]);
    }
    return no;
  }

  return o;
}

// Hàm tải một tài nguyên bất kỳ
export function forceDownload(url, filename) {
  const xhr = new XMLHttpRequest();
  const downloadFilename = filename || getFilename(url);

  xhr.open('GET', url, true);
  xhr.responseType = 'blob';
  xhr.onload = function () {
    const urlCreator = window.URL || window.webkitURL;
    const imageUrl = urlCreator.createObjectURL(this.response);
    const tag = document.createElement('a');

    tag.href = imageUrl;
    tag.download = downloadFilename;
    document.body.appendChild(tag);
    tag.click();
    document.body.removeChild(tag);
  };
  xhr.send();
}

// Hàm copy clipboard một chuỗi
export function copyClipboard(str) {
  const el = document.createElement('input');

  el.value = str;
  document.body.appendChild(el);
  el.select();
  document.execCommand('copy');
  document.body.removeChild(el);
}

// Thực thi hàm ngoài không ảnh hưởng tới promise
export function invokeOutside(func, ...params) {
  setTimeout(() => {
    if (isFunction(func)) {
      func(...params);
    }
  }, 0);
}

// Hàm render an toàn
export function safeRender(render, container, element, callback) {
  const childElm = document.createElement('div');

  childElm.style.width = '100%';
  childElm.style.height = '100%';

  element.innerHTML = '';
  element.appendChild(childElm);

  render(container, childElm, callback);
}

// Hàm get type của một giá trị
export function getTypeOf(val) {
  try {
    return {}.toString.call(val).split(' ')[1].replace(']', '').toLowerCase();
  } catch (ex) {
    return null;
  }
}

// Hàm get type của một giá trị
export function arrayMove(arr, old_index, new_index) {
  if (new_index >= arr.length) {
    var k = new_index - arr.length + 1;
    while (k--) {
      arr.push(undefined);
    }
  }

  arr.splice(new_index, 0, arr.splice(old_index, 1)[0]);

  return arr; // for testing
}

export function calculateSvgSize(width, height) {}

export function groupBy(arr = [], criteria) {
  if(isArray(arr)){
    const newObj = arr.reduce(function (acc, currentValue) {
      if (!acc[currentValue[criteria]]) {
        acc[currentValue[criteria]] = [];
      }
      acc[currentValue[criteria]].push(currentValue);
      return acc;
    }, {});
    return newObj;
  }

  return [];
}
