import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Button } from 'components/button';
import Modal from 'components/modal';
import PaymentModalContent from './PaymentModalContent';

PaymentMedodField.propTypes = {
    onChange: PropTypes.func,

};

PaymentMedodField.defaultProps = {
    onChange() {}
};

function PaymentMedodField(props) {
    const [openModal, setOpenModal] = useState(false);

    return (
        <div className='ui-button button-default size-default is-outline w-100 justify-content-between' style={{height: '46px', fontWeight: 'bold', paddingLeft: '12px'}}>
            <span>Bank Transfers</span>
            <Button type="default" outline={true} height={29} onClick={() => setOpenModal(true)}>Show all</Button>

            <Modal value={openModal} onClose={() => setOpenModal(false)}>
                <PaymentModalContent />
            </Modal>
        </div>
    );
}

export default PaymentMedodField;