import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import './style.scss';
import { FontAwesome } from 'components/icons';

Modal.propTypes = {
    value: PropTypes.bool,
    onClose: PropTypes.func,
};

Modal.defaultProps = {
    value: false,
    onClose() {}
};

function Modal(props) {
    const { value, children, onClose } = props;
    
    return (
        <div className={clsx('ui-modal', {'fade show': value})} style={{display: value ? 'block' : 'none'}}>
            <div className="ui-modal-dialog">
                <span className='ui-modal-close' onClick={() => onClose(false)}><FontAwesome icon="far fa-times-circle" /></span>
                <div className="ui-modal-content">
                    { children }
                </div>
            </div>
            <div className={clsx('ui-modal-backdrop', {'fade show': value})} onClick={() => onClose(false)}></div>
        </div>
    );
}

export default Modal;