/* eslint-disable no-mixed-spaces-and-tabs */
import { Component } from 'react';
import PropTypes from 'prop-types';
import { isEmpty, isFunction } from 'utils/validator';

import './style.scss';
class Switch extends Component {
    static propTypes = {
    	on: PropTypes.bool,
    	value: PropTypes.any,
    	onChange: PropTypes.func
    }

    static defaultProps = {
    	on: false,
    	onChange(){}
    }

    constructor(props) {
    	super(props);
        
    	this.state = {
    		on: this.props.on
    	};
    }
	
    componentWillReceiveProps(nextProps) {
    	this.setState({
    		on: nextProps.on
    	});
    }
	
    handleSelect = () => {
    	const { onChange, value } = this.props;
    	onChange(this.state.on, value);
    }

    render() {
    	const { on } = this.state;
		
    	return (
    		<div className={`switch ${on ? 'switch-checked' : ''}`}>
    			<div className="switch-track"></div>
    			<div className={`switch-underlay ${on ? 'on' : 'off'}`}>
    				<div className="switch-label">
    					<input 
    						type="checkbox" 
    						className="switch-control"
    						ref={c => this.refSwitch = c}
    						checked={this.state.on} 
    						onChange={this.handleSelect}
    					/>
    					<div className="switch-thumb"></div>
    				</div>
    			</div>
    		</div>
    	);
    }
}

export default Switch;