import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  isLoading: false,
  error: null,
  userInfo: null
};

export const authSlide = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    getCommonTk: (state, { payload }) => {},
    getCommonTkSuccess: (state, { payload }) => {},
    getUserToken: (state, { payload }) => payload,
    sendRegistrationCode: (state, { payload }) => {},
    sendRegistrationCodeSuccess: (state, { payload }) => {},
    register: (state, { payload }) => {},
    registerSuccess: (state, { payload }) => {},
    registerError: (state, {payload}) => {
      state.error = payload;
    },
    checkLoginInfo: (state, { payload }) => {},
    checkLoginInfoSuccess: (state, { payload }) => payload,
    checkLoginInfoError: (state, {payload}) => {
      state.error = payload;
    },
    login: (state, { payload }) => {},
    loginSuccess: (state, { payload }) => {},
    loginError: (state, {payload}) => {
      state.error = payload;
    },
    sendResetPasswordCode: (state, { payload }) => {},
    sendResetPasswordCodeError: (state, { payload }) => {},
    sendResetPasswordCodeSuccess: (state, { payload }) => {},
  }
});

// Actions
export const actions = authSlide.actions;

// Selectors
export const selectAuth = (state) => state.auth;

export default authSlide.reducer;
