import React, { useRef, useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Button, ButtonGroup } from 'components/button';
import { SvgIcon } from 'components/icons';
import { BitcoinIcon, EthCoinIcon, SortDown, SortRight, TethercoinIcon } from 'components/icons/svgs';

import "./style.scss";
import clsx from 'clsx';
import { Dropdown, DropdownItem, DropdownMenu, DropdownToggle } from 'components/dropdown';
import { Link } from 'react-router-dom';

BuySellButton.propTypes = {
    type: PropTypes.string,
    data: PropTypes.array
};

BuySellButton.defaultProps = {
    type: 'buy',
    data: []
};

function BuySellButton(props) {
    const ref = useRef()

    let { type } = props;

    const [toggle, setToggle] = useState(false);

    const handleToggle = (toggle) => {
        setToggle(!toggle);
    }

    useEffect(() => {
        const checkIfClickedOutside = e => {
          if (toggle && ref.current && !ref.current.contains(e.target)) {
            setToggle(false);
          }
        }
    
        document.addEventListener("mousedown", checkIfClickedOutside)
    
        return () => {
          // Cleanup the event listener
          document.removeEventListener("mousedown", checkIfClickedOutside);
        }
    }, [toggle])

    return (
        <div className='position-relative' ref={ref}>
            <Dropdown type='group'>
                <ButtonGroup>
                    <Button type="default" outline={true} bold={true} style={{textTransform: 'capitalize', padding: '0', borderRight: '1px solid #D7D7D7'}}><Link reloadDocument={true} style={{padding: '10px 16px'}} to={{pathname: `/${type}/btc`}}>{type}</Link></Button>
                    <DropdownToggle status={toggle} onToggle={handleToggle}></DropdownToggle>
                </ButtonGroup>
                
                <DropdownMenu open={toggle}>
                    <DropdownItem>
                        <Link reloadDocument={true} to={{pathname: `/${type}/btc`}} className='offer-dropdown__item d-block'>
                            <span className="d-flex flex-grow-1 align-items-center">
                                <SvgIcon icon={<BitcoinIcon />} size={40}/> 
                                <span className='d-flex flex-grow-1 pr-2 pl-3 ml-3 flex-column'>
                                    <strong>{type} Bitcoin</strong>
                                    <span>Search for offers to {type} Bitcoin</span>
                                </span>
                                <SvgIcon icon={<SortRight />} size={15} color="#D6D6D6"/>
                            </span>
                        </Link>
                        <Link reloadDocument={true} to={{pathname: `/${type}/eth`}} className='offer-dropdown__item d-block'>
                            <span className="d-flex flex-grow-1 align-items-center">
                                <SvgIcon icon={<EthCoinIcon />} size={40}/> 
                                <span className='d-flex flex-grow-1 pr-2 pl-3 ml-3 flex-column'>
                                    <strong>{type} Ethereum</strong>
                                    <span>Search for offers to {type} Ethereum</span>
                                </span>
                                <SvgIcon icon={<SortRight />} size={15} color="#D6D6D6"/>
                            </span>
                        </Link>
                        <Link reloadDocument={true} to={{pathname: `/${type}/usdt`}} className='offer-dropdown__item d-block'>
                            <span className="d-flex flex-grow-1 align-items-center">
                                <SvgIcon icon={<TethercoinIcon />} size={40}/> 
                                <span className='d-flex flex-grow-1 pr-2 pl-3 ml-3 flex-column'>
                                    <strong>{type} Tether</strong>
                                    <span>Search for offers to {type} USDT</span>
                                </span>
                                <SvgIcon icon={<SortRight />} size={15} color="#D6D6D6"/>
                            </span>
                        </Link>
                    </DropdownItem>
                </DropdownMenu>
            </Dropdown>
        </div>
    );
}
export default BuySellButton;