import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import SvgIcon from 'components/icons/SvgIcon';
import {SortUp, SortDown} from 'components/icons/svgs';
import { Button } from 'components/button';
import './style.scss';

DropdownToggle.propTypes = {
    style: PropTypes.object,
    width: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]),
    height: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]),
    iconOn: PropTypes.any,
    status: PropTypes.bool,
    iconOff: PropTypes.any,
    onToggle: PropTypes.func
};

DropdownToggle.defaultProps = {
    style: {},
    width: null,
    height: null,
    status: false,
    iconOff: <SvgIcon icon={<SortDown color="#333"></SortDown>} />,
    iconOn: <SvgIcon icon={<SortUp color="#333"></SortUp>} />,
    onToggle: () => {}
}

function DropdownToggle(props) {
    const { iconOn, iconOff, onToggle, children, style, width, height, status } = props;
    const [toggle, setToggle] = useState(status);

    useEffect(() => {
        setToggle(status);
    }, [status]);

    const handleClickToggle = () => {
        setToggle(!toggle);
        onToggle(toggle);
    }

    return (
        <Button type="default" outline={true} width={width} height={height} style={style} onClick={() => handleClickToggle()}>
            {children} { toggle ? iconOn : iconOff}
        </Button>
    );
}

export default DropdownToggle;