import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled from '@emotion/styled';

import './loading-icon.scss';
import { hexToRgb } from 'utils/converter';

class LoadingIcon extends Component {
	static propTypes = {
		icon: PropTypes.oneOf([
			'spinner',
			'ellipsis',
			'ring'
		]),
		size: PropTypes.number,
		color: PropTypes.string,
		style: PropTypes.object,
		onClick: PropTypes.func
	}

	static defaultProps = {
		icon: 'ring',
		size: 15,
		color: '#2ea2c7',
		style: {},
		onClick() {}
	}

	constructor(props) {
		super(props);

		this.renderIcon = this.renderIcon.bind(this);
		this.iconSpinner = this.iconSpinner.bind(this);
		this.iconEllipsis = this.iconEllipsis.bind(this);
		this.iconRing = this.iconRing.bind(this);
	}

	iconSpinner() {
		const { color, size } = this.props;
		const styleIcon = {
			margin: (size - 64) / 2,
			transform: `scale(${size / 64})`
		};
		const Item = styled.div({
			'&::after': {
				background: color ? `${color} !important` : null
			}
		});


		return (
			<div className="icon-spinner" style={styleIcon}>
				<Item />
				<Item />
				<Item />
				<Item />
				<Item />
				<Item />
				<Item />
				<Item />
				<Item />
				<Item />
				<Item />
				<Item />
			</div>
		);
	}

	iconEllipsis() {
		const { color, size } = this.props;
		const styleItem = {
			background: color,
			width: size,
			height: size
		};

		return (
			<div className="icon-ellipsis"
				style={{
					height: size
				}}
			>
				<div className="item-1" style={styleItem}></div>
				<div className="item-2" style={styleItem}></div>
				<div className="item-3" style={styleItem}></div>
			</div>
		);
	}

	iconRing() {
		const { color, size } = this.props;
		const styleIcon = {
			borderColor: hexToRgb(color, 0.2),
			borderLeftColor: color,
			width: size,
			height: size,
			borderWidth: size / 10
		};

		return <div className="icon-ring" style={styleIcon}></div>;
	}

	renderIcon() {
		const { icon } = this.props;

		switch(icon) {
			case 'spinner':
				return this.iconSpinner();
			case 'ellipsis':
				return this.iconEllipsis();
			case 'ring':
				return this.iconRing();
			default:
				return this.iconRing();
		}
	}

	render() {
		const { style } = this.props;

		return (
			<div className="ui-icon" style={{...style}}>
				{ this.renderIcon() }
			</div>
		);
	}
}

export default LoadingIcon;