import React, { useCallback, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import email from 'statics/images/email.png';
import { InputGroup } from 'components/input';
import { SvgIcon } from 'components/icons';
import { KeyIcon, SortRight, TimesCircle } from 'components/icons/svgs';
import { Button } from 'components/button';
import { Link } from 'react-router-dom';
import { verifyRegistrationCode } from 'services/api';
import { isArray, isString } from 'utils/validator';
import { secondsToMinutes } from 'utils/time';

RegisterVerifyCode.propTypes = {
    email: PropTypes.string,
    onChange: PropTypes.func,
};

RegisterVerifyCode.defaultProps = {
    email: '',
    onChange(){},
};

function RegisterVerifyCode(props) {
    const { onChange, email } = props;
    const [time, setTime] = useState(120);
    const [strTime, setStrTime] = useState('02:00');
    const [verifyCode, setVerifyCode] = useState(null);
    const [errors, setErrors] = useState(null);

    const handleInputChange = async (value) => {
        await setVerifyCode(value);
    }

    useEffect(() => {
        const interval = setInterval(() => {
            if(time > 0){
                setTime((time) => time - 1);
            }else{
                setTime(0);
            }
        }, 1000);

        return () => clearInterval(interval);
    }, []);

    useEffect(() => {
        setStrTime(secondsToMinutes(time));

        const handleSubmit = async () => {
            try {
                await verifyRegistrationCode({email, verifyCode});
                onChange({verifyCode});
            } catch (error) {
                setErrors(error);
            }
        };

        if(verifyCode && verifyCode.length === 6){
            handleSubmit();  
        }
    },[verifyCode, time]);

    return (
        <div className="auth-container animate__animated animate__slideInRight animate__faster">
            <div className="d-flex text-center">
                <div className="logo">
                    <img src={email} alt="" />
                </div>
            </div>
            <div className="title text-center">
                Email verification
            </div>
            <div className="explain text-center mb-5">
                A <strong>verification code</strong> has been sent to your email. Please fill in the code to continue
            </div>

            {
                errors && <div className="errors">
                {
                    (isString(errors) && errors) && <><SvgIcon icon={<TimesCircle />}></SvgIcon> <p>{errors}</p></>
                }
                {
                    (isArray(errors) && errors.length > 0) && <>
                        <SvgIcon icon={<TimesCircle />}></SvgIcon> { errors.map((error, key) => {
                            return <p key={key} className='err'>{error}</p>
                        }) }
                    </>
                }
                </div>
            }

            <div className="form">
                <div className="form-group mb-2">
                    <InputGroup prepend={<SvgIcon icon={<KeyIcon />} />} placeholder="Input verification code" onChange={(value) => handleInputChange(value)}/>
                </div>
                <div className="form-group text-end mb-4">
                    <strong style={{color: "#003399"}}>{strTime}</strong>
                </div>

                <div className="register-footer d-flex align-items-center mt-3">
                    Already have account? <Link to="/login" className='d-flex align-items-center mx-3'><SvgIcon icon={<SortRight />} size={6} color="#076BFF" style={{marginRight: '4px'}}></SvgIcon> Sign In</Link>
                </div>
            </div>
        </div>
    );
}

export default RegisterVerifyCode;