import React, { useRef, useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Button, ButtonGroup } from 'components/button';
import { SvgIcon } from 'components/icons';
import {
    UserBalanceIcon, 
    UserCenterIcon, 
    SortDown, 
    SortRight, 
    UserSettingIcon, 
    UserIcon, 
    UserKycIcon, 
    UserOfferIcon, 
    UserInvitationIcon, 
    LogoutIcon
} from 'components/icons/svgs';

import "./style.scss";
import clsx from 'clsx';
import { Dropdown, DropdownItem, DropdownMenu, DropdownToggle } from 'components/dropdown';
import { Link, useNavigate } from 'react-router-dom';

import * as apis from 'services/api';

BuySellButton.propTypes = {
    type: PropTypes.string,
    data: PropTypes.array
};

BuySellButton.defaultProps = {
    type: 'buy',
    data: []
};

function BuySellButton(props) {
    const ref = useRef();
    const [toggle, setToggle] = useState(false);
    const navigate = useNavigate();

    const handleToggle = (toggle) => {
        setToggle(!toggle);
    }

    const handleLogout = async (e) => {
        e.preventDefault();
        
        try {
            await apis.logout();
            navigate('/');
        } catch (error) {
            
        }

        return;
    }

    useEffect(() => {
        const checkIfClickedOutside = e => {
          if (toggle && ref.current && !ref.current.contains(e.target)) {
            setToggle(false);
          }
        }
    
        document.addEventListener("mousedown", checkIfClickedOutside)
    
        return () => {
          // Cleanup the event listener
          document.removeEventListener("mousedown", checkIfClickedOutside);
        }
    }, [toggle])

    return (
        <div className='position-relative ui-dropdown--user' ref={ref}>
            <Dropdown type='group'>
                <ButtonGroup>
                    <Button type="default" outline={true} height={35} classList="btn-user" onClick={() => handleToggle(toggle)}>
                        <SvgIcon icon={<UserIcon />} color="#999999" className="user-icon"/>
                        <SvgIcon icon={<SortDown />} size={16} color="#999999" style={{marginTop: '2px'}}/>
                    </Button>
                </ButtonGroup>
                
                <DropdownMenu open={toggle}>
                <>
                    <div className="dropitem-header">Hello <span>roifanno1</span></div>
                    <DropdownItem>
                        <Link to="/" className='offer-dropdown__item d-block'>
                            <span className="d-flex flex-grow-1 align-items-center">
                                <SvgIcon icon={<UserKycIcon />}/> 
                                <span style={{color: "#FA6B6B"}} className='me-2'>Go to KYC</span>
                                <SvgIcon icon={<SortRight />} size={7} color="#FA6B6B"/>
                            </span>
                        </Link>
                        <Link to="/" className='offer-dropdown__item d-block'>
                            <span className="d-flex flex-grow-1 align-items-center">
                                <SvgIcon icon={<UserCenterIcon />}/> 
                                <span className='me-2'>User center</span>
                            </span>
                        </Link>
                        <Link to="/" className='offer-dropdown__item d-block'>
                            <span className="d-flex flex-grow-1 align-items-center">
                                <SvgIcon icon={<UserInvitationIcon />}/> 
                                <span className='me-2'>MegaO Invitation</span>
                            </span>
                        </Link>
                        <Link to="/" className='offer-dropdown__item d-block'>
                            <span className="d-flex flex-grow-1 align-items-center">
                                <SvgIcon icon={<UserOfferIcon />}/> 
                                <span className='me-2'>My Offers</span>
                            </span>
                        </Link>
                        <Link to="/" className='offer-dropdown__item d-block'>
                            <span className="d-flex flex-grow-1 align-items-center">
                                <SvgIcon icon={<UserBalanceIcon />}/> 
                                <span className='me-2'>Balance</span>
                            </span>
                        </Link>
                        <Link to="/" className='offer-dropdown__item d-block'>
                            <span className="d-flex flex-grow-1 align-items-center">
                                <SvgIcon icon={<UserSettingIcon />}/> 
                                <span className='me-2'>Setting</span>
                            </span>
                        </Link>
                        <div className="separator"></div>
                        
                        <div onClick={(e) => handleLogout(e)} className='offer-dropdown__item d-block'>
                            <span className="d-flex flex-grow-1 align-items-center">
                                <SvgIcon icon={<LogoutIcon />}/> 
                                <span className='me-2'>Logout</span>
                            </span>
                        </div>
                    </DropdownItem>
                </>
                </DropdownMenu>
            </Dropdown>
        </div>
    );
}
export default BuySellButton;