import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import StarRating from 'components/star';
import { SvgIcon } from 'components/icons';
import { BitcoinIcon, ContactIcon, ShieldProtectIcon, TimeIcon, VietnamIcon } from 'components/icons/svgs';
import { Button } from 'components/button';
import clsx from 'clsx';
import {t} from '@lingui/macro';
import {trans} from 'config/LinguiConfig';
import { stringFormatPaymentMethod } from 'utils/string';
import { formatDisplayCoin, formatDisplayMoney, formatFiat } from 'utils/formater';
import {ADVERT_TYPE} from 'constants/advert';

FiatItem.propTypes = {
    type: PropTypes.number,
    data: PropTypes.object,
};

FiatItem.defaultProps = {
    type: ADVERT_TYPE.BUY,
    data: {}
}

function FiatItem(props) {
    const { data, type } = props;
    const { fOtcAccount } = data;

    const typeText = (type === ADVERT_TYPE.BUY ? trans(t`Buy`) : trans(t`Sell`));

    return (
        <div className="listing-item d-flex flex-row ui-shadow radius-2 mb-2">
            <div className="listing-col listing-col-1">
                <div className="mb-1">
                <Link to="/" className="name">
                    {data.accountName}
                </Link>
                </div>

                <div className="d-flex mb-2 rating">
                    <span>
                        <StarRating rating={Math.round(fOtcAccount.ratingPoint)} size={14} />
                    </span>
                    {fOtcAccount.ratingPoint}/5 ({fOtcAccount.totalRating})
                </div>
                <div className="d-flex merchant mb-2">
                    <span className="d-flex align-items-center">
                        <SvgIcon icon={<ShieldProtectIcon />}></SvgIcon>
                        Merchant
                    </span>
                    {
                        fOtcAccount.fhasrealvalidate && <span className="d-flex align-items-center">
                            <SvgIcon icon={<ContactIcon />}></SvgIcon>
                            Verified
                        </span>
                    }
                </div>
                <div className="user-online">
                {
                    fOtcAccount.online
                }
                <i className={clsx('fas fa-circle', {online: fOtcAccount.online})}></i> <span>{fOtcAccount.onLine ? `Online` : 'Offline'}</span>
                </div>
            </div>
            <div className="listing-col listing-col-2">
                <div className="d-flex align-item-center tranfer">
                    <strong>{ stringFormatPaymentMethod(data.subPaymentTypeName) }</strong>
                    <div className="flag">
                        <SvgIcon icon={<VietnamIcon />}></SvgIcon>
                    </div>
                </div>
                <div className='banks'>
                    {
                        data.bankNameList.map((bank, i) => <span key={`bank-${i}`}>{bank}{data.bankNameList.length - 1 !== i && `, `}</span>)
                    }
                </div>
            </div>
            <div className="listing-col listing-col-3">
                <div className="d-flex align-item-center">
                    <span className='amount'>{formatFiat(data.famount)}</span> <span className='coinname'>{data.fcoinName}</span>
                </div>
            </div>
            <div className="listing-col listing-col-4">
                <div className="total">{formatDisplayMoney(data.fprice)} {data.currencyName}</div>
                <div className="limit">Limits: {formatFiat(data.fminnum)}<span className='mx-1'>-</span>{formatFiat(data.fmaxnum)} VND</div>
                <div>
                    <Link reloadDocument={true} to="/" className={`ui-button button-default size-default justify-content-around btn-fiat btn-fiat-${type}`}>{typeText} <SvgIcon icon={<BitcoinIcon />} /></Link>
                </div>
            </div>
        </div>
    );
}

export default FiatItem;