import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  advertlistLoading: true,
  error: null,
  advertInfo: {}, //model PreCreatedInfo2
  info: {}, //InfoToCreateAd
  advertList: [],
  currentPage: 1,
  lastPage: 1,
  advert: {}, //Advert
  userBalances: [],
  userBuyMaxAmount: -1,
  userPaymentList: [],
  makerRatingList: [],
  ratingLastPage: -1,
  userAdLimit: 1
};

export const advertSlide = createSlice({
  name: 'advert',
  initialState,
  reducers: {
    getAdvertList: (state, { payload }) => {
      state.advertlistLoading = true;
    },
    getAdvertListSuccess: (state, { payload }) => {
      state.advertList = state.advertList.concat(payload.items);
      state.currentPage = payload.currentPage;
      state.lastPage = payload.lastPage;
      state.advertlistLoading = false;
    },
    getAdvertListError: (state, { payload }) => {
      state.advertlistLoading = false;
    },
  }
});

// Actions
export const {getAdvertList, getAdvertListSuccess, getAdvertListError} = advertSlide.actions;

// Selectors
export const selectAdvertLastPage = (state) => state.advert.lastPage;
export const selectAdvertListLoading = (state) => state.advert.advertlistLoading;
export const selectAdvertError = (state) => state.advert.error;
export const selectCurrentPage = (state) => state.advert.currentPage;
export const selectAdvertList = (state) => state.advert.advertList;

// export const {} = (state) => state.advert;

export default advertSlide.reducer;
