import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import * as apis from 'services/api';

const initialState = {
  userIsLoading: null,
  error: null,
  userInfo: null,
  userBalances: null,
  userTotalOrderActive: null,
};

export const getUserInfo = createAsyncThunk('user/getUserInfo', async (id) => {
  const res = await apis.getUserInfo(id);
  return res;
});

export const getUserBalances = createAsyncThunk('user/getUserBalances', async () => {
  const res = await apis.getUserBalances();
  return res;
});

export const getUserTotalOrderActive = createAsyncThunk('user/getTotalOrderActive', async () => {
  const res = await apis.getUserTotalOrderActive();
  return res;
});

export const userSlide = createSlice({
  name: 'user',
  initialState,
  reducers: {
    // getUserInfo: (state, { payload }) => {},
    // getUserBalances: (state, { payload }) => {},
    // getTotalOrderActive: (state, { payload }) => {}
  },
  extraReducers(builder) {
    builder.addCase(getUserInfo.fulfilled, (state, action) => {
      state.userInfo = action.payload;
      state.userIsLoading = false;
    });

    builder.addCase(getUserInfo.rejected, (state, action) => {
      state.error = action.error.message;
      state.userIsLoading = false;
    });

    builder.addCase(getUserBalances.fulfilled, (state, action) => {
      state.userBalances = action.payload;
    });

    builder.addCase(getUserBalances.rejected, (state, action) => {
      state.error = action.error.message
    });

    builder.addCase(getUserTotalOrderActive.fulfilled, (state, action) => {
      state.totalOrderActive = action.payload;
    });

    builder.addCase(getUserTotalOrderActive.rejected, (state, action) => {
      state.error = action.error.message
    });
  }
});

// Actions
export const actions = userSlide.actions;

// Selectors
export const selectUser = (state) => state.user;
export const selectUserInfo = (state) => state.user.userInfo;
export const selectUserBalances = (state) => state.user.userBalances;
export const selectUserTotalOrderActive = (state) => state.user.userTotalOrderActive;

export const selectUserIsLoading = (state) => state.user.userIsLoading;

export default userSlide.reducer;
