import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import RegisterSendCode from './RegisterSendCode';
import RegisterVerifyCode from './RegisterVerifyCode';
import RegisterInfo from './RegisterInfo';
import './style.scss';

Register.propTypes = {
    
};

function Register(props) {
    const [step, setStep] = useState('code');
    const [formInfo, setFormInfo] = useState({});

    function handleOnChange(data = {}, step) {
        setFormInfo({
            ...formInfo,
            ...data
        });

        if(step === 'code'){
            setStep('verify');
        }

        if(step === 'verify'){
            setStep('info');
        }
    }

    return (
        <div className='auth-page'>
            <div className="container-fluid">
                <div className="auth-form">
                    { step === 'code' && <RegisterSendCode onChange={(data) => handleOnChange(data, 'code')} /> }
                    { step === 'verify' && <RegisterVerifyCode email={formInfo.email} onChange={(data) => handleOnChange(data, 'verify')} /> }
                    { step === 'info' && <RegisterInfo email={formInfo.email} verifyCode={formInfo.verifyCode} onChange={(data) => handleOnChange(data, 'info')} /> }
                </div>
            </div>
        </div>
    );
}

export default Register;