import React, { useState } from 'react';
import PropTypes from 'prop-types';
import './style.scss';
import logo from 'statics/images/logo.png';
import SvgIcon from 'components/icons/SvgIcon';
import {EmailIcon, PasswordIcon, SortRight} from 'components/icons/svgs';
import { Button } from 'components/button';
import { Link } from 'react-router-dom';
import { Input, InputGroup } from 'components/input';
import { useDispatch, useSelector } from 'react-redux';
import * as Yup from 'yup';
import { Trans, t } from '@lingui/macro';
import { trans } from 'config/LinguiConfig';
import {selectAuth, actions} from 'redux/authSlide';
import LoginForm from './LoginForm';
import LoginValidate from './LoginValidate';

Login.propTypes = {
    
};

function Login(props) {
    const [step, setStep] = useState('form');
    const [formInfo, setFormInfo] = useState({});

    const handleChangeForm = (value) => {
        setFormInfo(value);
        setStep('validate');
    }

    return (
        <div className='auth-page'>
            <div className="container-fluid">
                <div className="auth-form">
                    { step === 'form' && <LoginForm onChange={(value) => handleChangeForm(value)} />}
                    { step === 'validate' && <LoginValidate params={formInfo} />}
                </div>
            </div>
        </div>
    );
}

export default Login;