import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import Checkbox from 'components/checkbox';
import { Button } from 'components/button';
import { Link } from 'react-router-dom';
import { BitcoinIcon, ContactIcon, ShieldProtectIcon, TimeIcon, VietnamIcon } from 'components/icons/svgs';
import { SvgIcon } from 'components/icons';
import StarRating from 'components/star';
import FiatItem from './FiatItem';
import { selectAdvertLastPage, selectCurrentPage, selectAdvertListLoading } from 'redux/advertSlide';

import '../style.scss';
import { useSelector } from 'react-redux';
import { ADVERT_TYPE } from 'constants/advert';
import LoadingIcon from 'components/icons/LoadingIcon';

FiatContent.propTypes = {
  data: PropTypes.array,
  currentFilter: PropTypes.object,
  onChange: PropTypes.func,
  type: PropTypes.number,
};

FiatContent.defaultProps = {
  data: [],
  currentFilter: {},
  type: ADVERT_TYPE.BUY,
  onChange: ()=>{}
}

function FiatContent(props) {
  const { 
    data,
    type,
    onChange,
    currentFilter
  } = props;

  const lastPage = useSelector(selectAdvertLastPage);
  const advertListLoading = useSelector(selectAdvertListLoading);

  const handleChangeFilter = (value) => {
   onChange({
     ...currentFilter,
     ...value
   })
  }

  const renderList = (data) => {
    return data.map((item, idx) => {
      return <FiatItem key={`fiat-item-${idx}`} data={item} type={type}/>
    })
  }
  
  return (
    <div className="fiat-main-wrapper flex-grow-1">
      <div className="fiat-main-container">
        <h1>Buy Bitcoin (BTC)</h1>
        <div className="desc mb-4">
          Trade virtually anything you own, from cars and gold to groceries and appliances, for Tether.
        </div>
        <div className="d-flex justify-content-between mb-2">
          <div></div>
          <div>
            <Checkbox boxSize={20} label="Show online user only" checked={currentFilter.onLine} onChange={(value) => handleChangeFilter({
              onLine: value
            })}/>
          </div>
        </div>
        <div className="fiat-listing-head ui-shadow d-flex flex-row radius-2 mb-2">
          <div className="listing-col">
            <span>Buy From</span>
          </div>
          <div className="listing-col">
            <span>Pay With</span>
          </div>
          <div className="listing-col">
            <span>Volume</span>
          </div>
          <div className="listing-col">
            <div>
              Rate per Bitcoin <i className="fas fa-question-circle" style={{ marginLeft: '6px' }}></i>
              <div className="buttons">
                <Button outline={true} height={30}>
                  <i className="fas fa-sort-amount-down"></i>
                </Button>
                <Button outline={true} height={30}>
                  <i className="fas fa-info-circle"></i>
                </Button>
              </div>
            </div>
          </div>
        </div>
        <div className="fiat-listing mb-4">
            {
              (advertListLoading && data.length === 0) && <div className='text-center py-5 fiat-loading'><LoadingIcon size={30} color="#003399" /></div>
            }
            {
              renderList(data)
            }
        </div>
        <div className="loadmore">
          <div className="d-flex justify-content-between">
            {
              (currentFilter.currentPage + 1 <= lastPage) ? <Button type="primary" height={38} disabled={advertListLoading} onClick={() => handleChangeFilter({
                currentPage: currentFilter.currentPage + 1
              })}>
                { advertListLoading && <LoadingIcon color="#fff" style={{marginRight: '3px'}}/> } Load More Offers
              </Button> : <div></div>
            }

            <div className="loadmore-right">
              <span>Didn't find a suitable offer?</span>
              <Button outline={true} height={38} style={{ paddingLeft: '0.9375rem', paddingRight: '0.9375rem' }}>
                Create your own offer
              </Button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default FiatContent;
