import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Input from './Input';

class Textarea extends Component {
	static propTypes = {
		value: PropTypes.oneOfType([
			PropTypes.string,
			PropTypes.number
		]),
		height: PropTypes.oneOfType([
			PropTypes.number,
			PropTypes.string
		]),
		width: PropTypes.oneOfType([
			PropTypes.number,
			PropTypes.string
		]),
		readOnly: PropTypes.bool,
		placeholder: PropTypes.string,
		style: PropTypes.object,
		maxLength: PropTypes.number,
		onChange: PropTypes.func,
		nativeRef: PropTypes.func,
		classList: PropTypes.string,
	}

	static defaultProps = {
		value: '',
		height: 180,
		width: '100%',
		placeholder: '',
		readOnly: false,
		style: {},
		maxLength: null,
		classList: '',
		onChange() {},
		nativeRef() {},
	}

	constructor(props) {
		super(props);
	}

	render() {
		const {
			value,
			width,
			height,
			placeholder,
			style,
			readOnly,
			onChange,
			maxLength,
			nativeRef,
			classList
		} = this.props;
	
		return (
			<Input
				type="textarea"
				readOnly={readOnly}
				value={value}
				width={width}
				height={height}
				placeholder={placeholder}
				style={style}
				onChange={onChange}
				classList={classList}
				maxLength={maxLength}
				nativeRef = {(c) => nativeRef(c)}
			/>
		);
	}
}

export default Textarea;