import { Component, isValidElement } from 'react';
import { isArray, isClass } from 'utils/validator';

import './style.scss';

class ButtonGroup extends Component {
	constructor(props) {
		super(props);
	}

	renderChildren = () => {
		const { children } = this.props;

		if (isArray(children)) {
			return children.filter(el => {
				if (isValidElement(el) && el.type.displayName === 'Button') {
					return true;
				}

				return false;
			});
		} else if (isValidElement(children) && children.type.displayName === 'Button') {
			return children;
		}

		return null;
	}

	render() {
		const { children } = this.props;

		return (
			<div className="ui-button-group ui-button-group--separator">
				{ children }
			</div>
		);
	}
}

export default ButtonGroup;