import { all, call, fork, put, take, takeLatest } from 'redux-saga/effects';
import * as apis from 'services/api';
import types from 'redux/actionTypes';
import LocalStorage from 'config/localStorage';
import moment from 'moment';
import { actions } from 'redux/appSlide';
import store from 'redux/store';
import { actionTypes } from 'redux-localstorage';
import GlobalStore from 'constants/global';

const getCoinList = function*({ payload } = { payload: { getNetworkFee: false } }) {
  try {
    const dataList = yield LocalStorage.getDataList('coinList');

    if (!dataList || payload?.getNetworkFee) {
      const params = {
        getNetworkFee: payload?.getNetworkFee ?? false
      };

      const res = yield call(apis.getCoinList, params);

      if (res) {
        yield LocalStorage.setDataList('coinList', {
          data: res,
          expiredTime: moment()
            .add(1, 'd')
            .format()
        });
        
        yield put(actions.getCoinListSuccess(res));
      }
    } else {
      yield put(actions.getCoinListSuccess(dataList));
    }
  } catch (error) {
    // yield put(actions.getCoinListError(error));
    // showAlert(undefined, error);
  }
};

const getCountryList = function*() {
  try {
    const dataList = yield LocalStorage.getDataList('countryList');
    
    if (!dataList) {
      const res = yield call(apis.getCountryList);
      if (res) {
        yield LocalStorage.setDataList('countryList', {
          data: res,
          expiredTime: moment()
            .add(30, 'd')
            .format()
        });
        // yield put(actions.getCountryListSuccess(res));
      }
    } else {
      return dataList.data;
      // yield put(actions.getCountryListSuccess(dataList.data));
    }
  } catch (error) {
    // yield put(actions.getCountryListError(error));
    // showAlert(undefined, error);
  }
};

const getCurrencyList = function*() {
  try {
    const dataList = yield LocalStorage.getDataList('currencyList');

    if (!dataList) {
      const res = yield call(apis.getCurrencyList);
      
      if (res) {
        const data = res.map(i => ({ 
          ...i, 
          nameStr: `${i.name} - ${i.fullName}` 
        }));
        
        yield LocalStorage.setDataList('currencyList', {
          data: data,
          expiredTime: moment()
            .add(30, 'd')
            .format()
        });
        // yield put(actions.getCurrencyListSuccess(data));
      }
    } else {
      // yield put(actions.getCurrencyListSuccess(dataList.data));
      return dataList;
    }
  } catch (error) {
    // yield put(actions.getCurrencyListError(error));
    // showAlert(undefined, error);
  }
};

const getPaymentMethodList = function*() {
    try {
      const dataList = yield LocalStorage.getDataList('paymentMethodList');
      
      if (!dataList) {
        const res = yield call(apis.getPaymentMethodList);
        if (res) {
          yield LocalStorage.setDataList('paymentMethodList', {
            data: res,
            expiredTime: moment()
              .add(1, 'd')
              .format()
          });
          // yield put(actions.getPaymentMethodListSuccess(res));
        }
      } else {
        // yield put(actions.getPaymentMethodListSuccess(dataList.data));
      }
    } catch (error) {
    //   yield put(actions.getPaymentMethodListError(error));
    //   showAlert(undefined, error);
    }
};

const watchers = function* () {
  yield takeLatest(types.GET_PAYMENT_METHOD_LIST, getPaymentMethodList);
  yield takeLatest(types.GET_COIN_LIST, getCoinList);
  yield takeLatest(types.GET_COUNTRY_LIST, getCountryList);
  yield takeLatest(types.GET_CURRENCY_LIST, getCurrencyList);
};

export default watchers();