import React, { useEffect } from 'react';
import LocalStorage from 'config/localStorage';
import store from 'redux/store';
import { authSlide } from 'redux/authSlide';
import { getUserInfo, getUserBalances, getUserTotalOrderActive } from 'redux/userSlide';
import { uuid } from 'utils/externals';
import GlobalStore from 'constants/global';
import moment from 'moment';
import * as apis from 'services/api';
import { appSlide } from 'redux/appSlide';

export const useAppInit = (setIsLoading) => {
  const getUserData = async () => {
    const userId = await LocalStorage.getUserId();
    store.dispatch(getUserInfo(userId));
    store.dispatch(getUserBalances());
    store.dispatch(getUserTotalOrderActive());
  
    // const { success, error, ...data } = await handleApi(getChatAccessToken());
    // if (success) {
    //   signInFirebaseWithToken(data.data);
    // }
  };
  
  const getCommonData = async () => {
    // user geo
    const userGeo = await LocalStorage.getUserGeo();
    if (userGeo && userGeo.expiredTime && moment().diff(moment(userGeo.expiredTime)) < -300000) {
      const { currencyCode, countryCode } = userGeo;
      GlobalStore.currencyCode = currencyCode;
      GlobalStore.countryCode = countryCode;

      // store.dispatch(app.actions.getGeoByIpSuccess(userGeo));
    } else {
      const res = await apis.getGeo();

      if (res) {
        //   store.dispatch(app.actions.getGeoByIpSuccess(res));

        // get language for ip devices
        //   const countOpenApp = await LocalStorage.getFromLocal('countOpenApp');

        // lang
        //   const lang = await LocalStorage.getLanguage();

        //   if (countOpenApp === null && lang === '') {
        //     const langVI = 'vi';
        //     const langEN = 'en';
        //     if (res.countryCode === 'VN') {
        //       store.dispatch(changeLanguage(langVI));
        //     } else {
        //       store.dispatch(changeLanguage(langEN));
        //     }
        //     GlobalStore.countOpenApp = 1;
        //     await LocalStorage.setToLocal('countOpenApp', 1);
        //   }

        // Set user geo
        GlobalStore.currencyCode = res.currencyCode;
        GlobalStore.countryCode = res.countryCode;

        await LocalStorage.setUserGeo({
          currencyCode: res.currencyCode,
          countryCode: res.countryCode,
          expiredTime: moment().add(7, 'd').format(),
        });
      } else {
        //   store.dispatch(changeLanguage('en'));
        //   GlobalStore.countOpenApp = 1;
        //   await LocalStorage.setToLocal('countOpenApp', 1);
      }
    }

    await store.dispatch(appSlide.actions.getCoinList());
    await store.dispatch(appSlide.actions.getCurrencyList());
    await store.dispatch(appSlide.actions.getCountryList());
    await store.dispatch(appSlide.actions.getPaymentMethodList());
  };
  
  const init = async (isGuest = false, cb = () => {}) => {
    try {
      await getCommonData();
      if (!isGuest) {
        getUserData();
      }
    } catch (error) {
      console.log(error);
    } finally {
      setIsLoading(false);
      cb?.();
    }
  };
  
  useEffect(() => {
    const getInit = async () => {
      const localUuid = await LocalStorage.getUuid();
      if (localUuid) {
        GlobalStore.uuid = localUuid;
      } else {
        GlobalStore.uuid = await uuid();
        LocalStorage.setUuid(GlobalStore.uuid);
      }

      // lang
      const lang = await LocalStorage.getLanguage();
      GlobalStore.lang = lang;

      // user token
      const token = await LocalStorage.getToken();
      
      if (!token) {
        store.dispatch(authSlide.actions.getCommonTk({
          onSuccess: (res) => {
            init(true);
          }
        }));
      } else {
        const refreshToken = await LocalStorage.getRefreshToken();
        const tokenExpired = await LocalStorage.getTokenExpired();
        const userId = await LocalStorage.getUserId();

        GlobalStore.token = token;
        GlobalStore.tokenExpired = tokenExpired;
        GlobalStore.refreshToken = refreshToken;

        if (userId) {
          GlobalStore.isLoginUser = true;
        }

        store.dispatch(
          authSlide.actions.getUserToken({
            onSuccess: () => {
              init()
            },
            onError: () => {
              init(true);
            },
          })
        );
      }
    };

    getInit();
  }, []);
};
