import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { isFunction } from 'utils/validator';
import styled from '@emotion/styled';

import './style.scss';

class Radio extends Component {
	static displayName = 'Radio';
	
	static propTypes = {
		checked: PropTypes.bool,
		value: PropTypes.any.isRequired,
		buttonColor: PropTypes.string,
		buttonRadius: PropTypes.number,
		color: PropTypes.string,
		fontSize: PropTypes.number,
		style: PropTypes.object,
		onChange: PropTypes.func
	}

	static defaultProps = {
		checked: false,
		buttonColor: '#2ea2c7',
		buttonRadius: 10,
		color: '#333',
		fontSize: 13,
		style: {},
		onChange() {}
	}

	constructor(props) {
		super(props);

		this.handleChange = this.handleChange.bind(this);
	}

	handleChange() {
		const { onChange, value } = this.props;

		if (isFunction(onChange)) {
			onChange(value);
		}
	}

	render() {
		const {
			children,
			fontSize,
			color,
			buttonColor,
			buttonRadius,
			value,
			style,
			checked,
			groupId
		} = this.props;

		const styleLabel = {
			fontSize,
			color
		};

		const styleInput = {
			backgroundColor: '#fff',
			width: buttonRadius * 2,
			height: buttonRadius * 2,
			'&::after': {
				backgroundColor: buttonColor,
				width: (buttonRadius * 2) - 8,
				height: (buttonRadius * 2) - 8,
				left: 3,
				top: 3
			}
		};

		const RadioInput = styled.div(styleInput);

		return (
			<label className="ui-radio radio-button" style={style}>
				<input type="radio"
					name={groupId}
					checked={checked}
					value={value}
					onChange={this.handleChange}
				/>

				<RadioInput className="radio-input" />

				{
					children ?
						<span className="radio-label" style={styleLabel}>
							{ children }
						</span>
						: ''
				}
			</label>
		);
	}
}

export default Radio;