import { call, put, takeLatest } from 'redux-saga/effects';
import * as apis from 'services/api';
import types from 'redux/actionTypes';
import LocalStorage from 'config/localStorage';
import moment from 'moment';
import { actions } from 'redux/authSlide';
import store from 'redux/store';
import GlobalStore from 'constants/global';
import {uuid} from 'utils/externals'
import history from 'config/history';

const resetToken = async () => {
  await LocalStorage.clearToken();
};

const getCommonTk = function* ({payload} = { isReset: false, onSuccess: () => {} }) {
  try {
    const { isReset } = payload;
    const oldToken = yield LocalStorage.getGuestToken();
    const oldTokenExpired = yield LocalStorage.getTokenExpired();

    let isExpired = true;
    if (oldToken && oldTokenExpired) {
      isExpired = moment().diff(moment(oldTokenExpired)) >= -300000; // <= 5 minutes until expired
    }

    if (!isExpired && !isReset) {
      GlobalStore.token = oldToken;
      GlobalStore.tokenExpired = oldTokenExpired;
      GlobalStore.isLoginUser = false;
      
      if (payload && payload.onSuccess) {
        payload.onSuccess();
      }
    } else {
      yield resetToken();
      const res = yield call(apis.getCommonTk);

      if (res && res.token) {
        const { token, token_expire } = res;

        GlobalStore.token = token;
        GlobalStore.tokenExpired = token_expire;
        yield LocalStorage.setGuestToken(token);
        yield LocalStorage.setTokenExpired(token_expire);

        // yield put(actions.getCommonTkSuccess(res));

        if (payload && payload.onSuccess) {
          payload.onSuccess();
        }
      }
    }
  } catch (error) {
    console.log(error);
  }
};

const sendRegistrationCode = function* ({ payload }) {
  try {
    yield call(apis.sendRegistrationCode, payload);
  } catch (error) {}
};

const register = function*({ payload } = {
  email: '',
  password: '',
  verifyCode: '',
  inviteCode: '',
  nickname: ''
}) {
  try {
    const { ...data } = payload;
    let currencyName = 'USD', location = '';

    // Get Geo
    const geoRes = yield call(apis.getGeo);
    
    if (geoRes) {
      GlobalStore.currencyCode = geoRes.currencyCode;
      GlobalStore.countryCode = geoRes.countryCode;
      
      yield LocalStorage.setUserGeo({
        currencyCode: geoRes.currencyCode,
        countryCode: geoRes.countryCode,
        expiredTime: moment()
          .add(7, 'd')
          .format()
      });

      currencyName = geoRes.currencyCode;
      location = geoRes.countryCode;
    }

    let deviceId = LocalStorage.getUuid() ?? uuid();

    const res = yield call(apis.register, {
      ...data,
      uuid: deviceId,
      location,
      currencyName
    });
   

    if (res && res.token) {
      const { token, refreshToken, token_expire, user } = res;
      GlobalStore.token = token;
      GlobalStore.isLoginUser = true;
      GlobalStore.refreshToken = refreshToken;
      GlobalStore.tokenExpired = token_expire;
      yield LocalStorage.setToken(token);
      yield LocalStorage.setRefreshToken(refreshToken);
      yield LocalStorage.setTokenExpired(token_expire);
      yield LocalStorage.setUserId(user.fid);
      yield LocalStorage.setUser(user);
      
      yield put(actions.registerSuccess(res));
    }
  } catch (error) {
    yield put(actions.registerError(error));
  }
};

const getUserToken = function*({ payload } = { onSuccess: () => {}, onError: () => {} }) {

  try {
    const oldToken = yield LocalStorage.getToken();
    const oldTokenExpired = yield LocalStorage.getTokenExpired();
    const oldRefreshToken = yield LocalStorage.getRefreshToken();
    const oldRefreshTokenExpired = yield LocalStorage.getRefreshTokenExpired();

    let isExpired = true,
      isRefreshExpired = false;
    if (oldToken && oldTokenExpired) {
      isExpired = moment().diff(moment(oldTokenExpired)) >= -300000; // <= 5 minutes until expired
    }
    if (oldRefreshToken && oldRefreshTokenExpired) {
      isRefreshExpired = moment().diff(moment(oldRefreshTokenExpired)) >= -300000; // <= 5 minutes until expired
    }

    if (!isExpired) {

      // yield put(actions.getUserTokenSuccess());

      if (payload && payload.onSuccess) {
        payload.onSuccess();
      }
    } else if (isRefreshExpired) {
      throw 'Refresh Token Expired';
    } else {
      let did = yield LocalStorage.getUuid();
      if(!did) did = uuid();

      const params = {
        uuid: did,
        refreshToken: oldRefreshToken
      };

      const res = yield call(apis.refreshUserToken, params);

      if (res && res.token) {
        const { token, token_expire, refreshToken, refreshToken_expire } = res;
        GlobalStore.token = token;
        GlobalStore.tokenExpired = token_expire;
        GlobalStore.refreshToken = refreshToken;

        yield LocalStorage.setToken(token);
        yield LocalStorage.setTokenExpired(token_expire);
        yield LocalStorage.setRefreshToken(refreshToken);
        yield LocalStorage.setRefreshTokenExpired(refreshToken_expire);

        // yield put(actions.getUserTokenSuccess(res));

        if (payload && payload.onSuccess) {
          payload.onSuccess();
        }
      }
    }
  } catch (error) {
    yield put(
      actions.getCommonTk({
        isReset: true,
        onSuccess: () => {
          if (payload && payload.onError) {
            payload.onError();
          }
        }
      })
    );
    yield put(actions.getUserTokenError(error));
    // showAlert(undefined, error);
  }
};

const sendResetPasswordCode = function*({ payload } = { payload: { email: '' } }) {
  try {
    const { email } = payload;
    yield call(apis.sendResetPasswordCode, email);
    yield put(actions.sendResetPasswordCodeSuccess());
    
    // NavigationService.navigate(ScreenKey.FORGOT_PASSWORD_SUCCESS);
  } catch (error) {
    yield put(actions.sendResetPasswordCodeError(error));
    // showAlert(undefined, error);
  }
};

const checkLoginInfo = function*({ payload } = { payload: { email: '', password: '' } }) {
  try {
    const { meta, ...data } = payload;
    data.deviceName = 'web';
    
    const res = yield call(apis.checkLoginInfo, data);
    
    if (res) {
      yield put(actions.checkLoginInfoSuccess(res));
    }
  } catch (error) {
    console.log(error);
    yield put(actions.checkLoginInfoError(error));
  }
};

const login = function*({
  payload
} = {
  payload: {
    email: '',
    password: '',
    verifyCode: '',
    isNewDevice: false
  }
}) {
  try {
    const { meta, ...data } = payload;
    const did = LocalStorage.getUuid();

    const deviceId = yield (did ? did : uuid());
    data.uuid = deviceId;

    const res = yield call(apis.login, data);
    
    if (res) {
      const { token, refreshToken, token_expire, refreshToken_expire, user } = res;
      GlobalStore.token = token;
      GlobalStore.refreshToken = refreshToken;
      GlobalStore.isLoginUser = true;
      GlobalStore.tokenExpired = token_expire;
      GlobalStore.isEscrowGuest = false;
      
      yield LocalStorage.setToken(token);
      yield LocalStorage.setUserId(user.fid);
      yield LocalStorage.setRefreshToken(refreshToken);
      yield LocalStorage.setTokenExpired(token_expire);
      yield LocalStorage.setRefreshTokenExpired(refreshToken_expire);

      yield put(actions.loginSuccess(res));
    }
  } catch (error) {
    yield put(actions.loginError(error));
  }
};

const watchers = function* () {
  yield takeLatest(types.SEND_REGISTRATION_CODE, sendRegistrationCode);
  yield takeLatest(types.SEND_RESET_PASSWORD_CODE, sendResetPasswordCode);
  yield takeLatest(types.GET_COMMON_TK, getCommonTk);
  yield takeLatest(types.REGISTER, register);
  yield takeLatest(types.GET_USER_TOKEN, getUserToken);
  yield takeLatest(types.LOGIN, login);
  yield takeLatest(types.CHECK_LOGIN_INFO, checkLoginInfo);
};

export default watchers();
