import { isString, isEmpty } from './validator';

// Hàm chuyển ảnh sang dạng base64
export function imageToBase64(img) {
	try {
		const canvas = document.createElement('canvas');
		const ctx = canvas.getContext('2d');

		canvas.width = img.width;
		canvas.height = img.height;
		ctx.drawImage(img, 0, 0);

		return canvas.toDataURL();
	} catch(err) {
		console.error(err);
		return '';
	}
}

// Hàm chuyển mã màu rgb sang hex
export function rgbToHex(r, g, b) {
	try {
		const componentToHex = (c) => {
			const hex = c.toString(16);
			return hex.length == 1 ? `0${hex}` : hex;
		};

		return `#${componentToHex(r)}${componentToHex(g)}${componentToHex(b)}`;
	} catch(err) {
		console.error(err);
		return null;
	}
}

// Hàm chuyển mã màu hex sang rgb
export function hexToRgb(hex, alpha) {
	try {
		if (isString(hex)) {
			const shorthandRegex = /^#?([a-f\d])([a-f\d])([a-f\d])$/i;
			const newHex = hex.replace(shorthandRegex, function (m, r, g, b) {
				return r + r + g + g + b + b;
			});
			const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(newHex);
			const newAlpha = (!isEmpty(alpha) && !isNaN(alpha))
				? parseFloat(alpha) : 1;

			return result ? `rgba(${parseInt(result[1], 16)},${parseInt(result[2], 16)},${parseInt(result[3], 16)},${newAlpha})` : null;
		}

		return null;
	} catch(err) {
		console.error(err);
		return null;
	}
}