import React, { useState } from 'react';
import { Routes, Route, Link } from 'react-router-dom';
import 'statics/styles/_variables.scss';
import 'statics/styles/app.scss';

import { Helmet } from 'react-helmet-async';
// import routes from 'pages/routes';
import HomePage from 'pages/home';
import CreateOfferPage from 'pages/create-offer';
import NotFound from 'pages/404';
import Header from 'components/header';
import Footer from 'components/footer';
import Login from 'pages/auth/Login';
import Register from 'pages/auth/Register';
import Fiat from 'pages/fiat';
import { useAppInit } from 'hooks/useAppInit';
import OtcMakeTerms from 'pages/otc/MakeTerms';
import OtcOffer from 'pages/otc/Offer';
import 'animate.css';
import ForgotPassword from 'pages/auth/ForgotPassword';
import Welcome from 'pages/auth/Welcome';
import ValidateResetEmail from 'pages/auth/ValidateResetEmail';
import { ADVERT_TYPE } from 'constants/advert';

function App() {
  const [isLoading, setIsLoading] = useState(true);
  useAppInit(setIsLoading);

  return (
    <div className="App">
      {
        !isLoading && <>
          <Helmet>
            <meta charSet="utf-8" />
            <title>My Title 11</title>
            <link rel="canonical" href="http://mysite.com/example" />
          </Helmet>
          
          <Header></Header>

          <Routes>
            <Route path="/" element={<HomePage />} />
            <Route path="/create-an-offer" element={<CreateOfferPage />} />
            <Route path="/login" element={<Login />} />
            <Route path="/register" element={<Register />} />
            <Route path="/reset_email" element={<ForgotPassword />} />
            <Route path="/validate/reset_email" element={<ValidateResetEmail />} />
            <Route path="/buy/:id" element={<Fiat type={ADVERT_TYPE.BUY} />} />
            <Route path="/sell/:id" element={<Fiat type={ADVERT_TYPE.SELL} />} />
            <Route path="/otc/maker_terms" element={<OtcMakeTerms />} />
            <Route path="/otc/offer" element={<OtcOffer />} />
            <Route path="/user/welcome" element={<Welcome />} />
            <Route path="*" element={<NotFound />}></Route>
          </Routes>

          <Footer></Footer>
        </>
      }
    </div>
  );
}

export default App;
