import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Dialog from './Dialog';
import { isString } from 'utils/validator';

import './style.scss';

import { FontAwesome } from '../icons';

class ModalBox extends Component {
	static propTypes = {
		title: PropTypes.string,
		width: PropTypes.number,
		height: PropTypes.number,
		children: PropTypes.any,
		open: PropTypes.bool,
		onClose: PropTypes.func,
		onConfirm: PropTypes.func,
		onOpen: PropTypes.func,
		onCancel: PropTypes.func
	}
	
	static defaultProps = {
		title: 'Đăng nhập',
		width: 520,
		height: 'auto',
		children: null,
		open: true,
		onClose() {},
		onConfirm() {},
		close() {},
		onCancel() {}
	}

	constructor(props) {
		super(props);
	}

	render() {
		const {
			title,
			width,
			height,
			children,
			open,
			onClose,
			onConfirm,
			onOpen,
			onCancel
		} = this.props;

		const dialogConfig = {
			title,
			width,
			height,
			// footer: {
			// 	[__('Đồng ý')](dialog) {
			// 		dialog.close();
			// 		onConfirm();
			// 	},
			// 	[__('Hủy')](dialog) {
			// 		dialog.close();
			// 		onCancel();
			// 	}
			// },
			onClose,
            onOpen,
            showFooter: false,
            // showHeader: false,
            visible: open
		};

		return (
			<Dialog {...dialogConfig}>
				<div className="dialog-modal">
					<div className="modal-content">
						{
							isString(children) ?
								<span dangerouslySetInnerHTML={{
									__html: children
								}}></span>
								: children
						}
					</div>
				</div>
			</Dialog>
		);
	}
}

export default ModalBox;