import fetchApi from 'config/apiConfig';

const METHOD = {
  POST: 'post',
  GET: 'get',
  PATCH: 'PATCH',
  PUT: 'put',
  DELETE: 'delete',
};

/**
 * Convert properties from null to empty string
 */
const transformData = (data = []) => {
  let result = [];
  if (data.length > 0) {
    result = data.map((i) => {
      if (i == null) {
        return '';
      }
      return i.toString();
    });
  }
  return result;
};

// Auth
export const getCommonTk = () => {
  return fetchApi('/common/tk', null, METHOD.POST);
};

export const refreshUserToken = (params = { uuid: '', refreshToken: '' }) => {
  return fetchApi('/users/refresh-access-token', params, METHOD.POST);
};

export const sendRegistrationCode = async (email = '') => {
  return fetchApi('/users/send_reg_email', { data: transformData([email]) }, METHOD.POST);
};

export const verifyRegistrationCode = (params = { email: '', verifyCode: '' }) => {
  const { email, verifyCode } = params;
  return fetchApi('/users/validate_reg_email', { data: transformData([email, verifyCode]) }, METHOD.POST);
};

export const register = (
  params = {
    email: '',
    password: '',
    verifyCode: '',
    inviteCode: '',
    nickname: '',
    uuid: '',
    location: '',
    currencyName: '',
  }
) => {
  const { email, password, nickname, uuid, verifyCode, inviteCode, location, currencyName } = params;
  return fetchApi(
    '/users/register',
    {
      data: transformData([email, password, verifyCode, uuid, nickname, inviteCode, location, currencyName]),
    },
    METHOD.POST
  );
};

