import React, { useState } from 'react';
import PropTypes from 'prop-types';
import './style.scss';
import logo from 'statics/images/logo.png';
import SvgIcon from 'components/icons/SvgIcon';
import {EmailIcon, PasswordIcon, SortRight, TimesCircle} from 'components/icons/svgs';
import { Button } from 'components/button';
import { Link } from 'react-router-dom';
import { Input, InputGroup } from 'components/input';
import { useDispatch, useSelector } from 'react-redux';
import * as Yup from 'yup';
import { Trans, t } from '@lingui/macro';
import { trans } from 'config/LinguiConfig';
import {selectAuth, actions} from 'redux/authSlide';
import gaSvg from 'statics/images/ga-authentication.svg'
import { isArray, isString } from 'utils/validator';
import LocalStorage from 'config/localStorage';
import GlobalStore from 'constants/global';
import * as apis from 'services/api';
import { useNavigate } from "react-router-dom";
import {md5} from 'utils/externals';

LoginValidate.propTypes = {
    params: PropTypes.object
};

LoginValidate.defaultProps = {
    params: {}
}

function LoginValidate(props) {
    const { params } = props;
    const [errors, setErrors] = useState([]);
    const [formInfo, setFormInfo] = useState({});
    const [verifyCode, setVerifyCode] = useState('');
    const authState = useSelector(selectAuth);
    const dispatch  = useDispatch();
    const navigate = useNavigate();

    const login = async function(params = {
          email: '',
          password: '',
          verifyCode: '',
          isNewDevice: ''
        }
      ) {
        try {
          const { meta, ...data } = params;
          const deviceId = await LocalStorage.getUuid();
          const deviceName = 'web';

          data.uuid = deviceId;
          data.deviceName = deviceName;

          console.log(data);
          
          const res = await apis.login(data);

          if (res) {
            const { token, refreshToken, token_expire, refreshToken_expire, user } = res;
            GlobalStore.token = token;
            GlobalStore.refreshToken = refreshToken;
            GlobalStore.isLoginUser = true;
            GlobalStore.tokenExpired = token_expire;
            GlobalStore.isEscrowGuest = false;

            await LocalStorage.setToken(token);
            await LocalStorage.setUserId(user.fid);
            await LocalStorage.setRefreshToken(refreshToken);
            await LocalStorage.setTokenExpired(token_expire);
            await LocalStorage.setRefreshTokenExpired(refreshToken_expire);
            
            navigate('/buy/btc');
          }
        } catch (error) {
            setErrors(error);
        }
      };

    const handleSubmit = async () => {
        login({
            email: params.email,
            password: md5(params.password),
            verifyCode,
            isNewDevice: ''
        });
    }

    return (
        <div className="auth-container animate__animated animate__slideInRight animate__faster">
            <div className="d-flex text-center">
                <div className="logo">
                    <img src={gaSvg} alt="" />
                </div>
            </div>
            <div className="title text-center">
                Google Authenticator
            </div>
            <div className="explain text-center mb-5">
                Mở app <strong>Google Authenticator</strong> trên điện thoại của bạn để nhận mã xác nhận
            </div>

            {
                errors && <div className="errors">
                {
                    (isString(errors) && errors) && <><SvgIcon icon={<TimesCircle />}></SvgIcon> <p>{errors}</p></>
                }
                {
                    (isArray(errors) && errors.length > 0) && <>
                        <SvgIcon icon={<TimesCircle />}></SvgIcon> { errors.map((error, key) => {
                            return <p key={key} className='err'>{error}</p>
                        }) }
                    </>
                }
                </div>
            }
            
            <div className="form">
                <div className="form-group mb-4">
                    <InputGroup onChange={(value) => setVerifyCode(value)} prepend={<SvgIcon icon={<EmailIcon />} />} height={40} placeholder="Nhập mã GA"/>
                </div>

                <div className="form-group">
                    <Button type="primary" classList="position-relative" style={{width: "100%"}} onClick={() => handleSubmit()}>Xác nhận</Button>
                </div>

                <div className="form-group">
                    <div className="register-footer d-flex align-items-center mt-3">
                        <Link to="/reset_email" className='d-flex align-items-center me-4'><SvgIcon icon={<SortRight />} size={6} color="#076BFF" style={{marginRight: '4px'}}></SvgIcon> Forgot password?</Link>
                    </div>
                </div>

                <div className="form-group">
                    <div className="register-footer d-flex align-items-center mt-1">
                        <Link to="/register" className='d-flex align-items-center'><SvgIcon icon={<SortRight />} size={6} color="#076BFF" style={{marginRight: '4px'}}></SvgIcon> Sign up</Link>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default LoginValidate;