import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import './style.scss';
import logo from 'statics/images/logo.png';
import SvgIcon from 'components/icons/SvgIcon';
import {EmailIcon, PasswordIcon, SortRight, TimesCircle} from 'components/icons/svgs';
import { Button } from 'components/button';
import { Link } from 'react-router-dom';
import { Input, InputGroup } from 'components/input';
import { useDispatch, useSelector } from 'react-redux';
import * as Yup from 'yup';
import { Trans, t } from '@lingui/macro';
import { trans } from 'config/LinguiConfig';
import {selectAuth, actions} from 'redux/authSlide';
import { isArray, isString } from 'utils/validator';
import {md5} from 'utils/externals';
import * as apis from 'services/api';

Login.propTypes = {
    onChange: PropTypes.func
};

Login.defaultProps = {
    onChange(){}
};

function Login(props) {
    const {onChange} = props;

    const [errors, setErrors] = useState([]);
    const [formInfo, setFormInfo] = useState({});

    const authState = useSelector(selectAuth);
    const dispatch  = useDispatch();

    useEffect(() => {
        setErrors(authState.error);
    }, [authState.error]);

    const schema = Yup.object().shape({
        email: Yup.string()
            .email(trans(t`Email không đúng định dạng`))
            .required(trans(t`Vui lòng nhập email`)),
        password: Yup.string().required(trans(t`Vui lòng nhập mật khẩu`))
    });

    const handleChangeInfo = (field = '', value = '') => {
        const newData = {};
        newData[field] = value;

        setFormInfo({
            ...formInfo,
            ...newData
        });
    }

    const checkLoginInfo = async (payload = { email: '', password: '' }) => {
        try {
          const { meta, ...data } = payload;
          data.deviceName = 'web';
          
          await apis.checkLoginInfo({
              ...data,
              password: md5(formInfo.password)
          });
          onChange(data);
        } catch (error) {
          setErrors(error);
        }
      };

    const handleSubmit = async () => {
        await schema.validate(formInfo).then(() => {
            checkLoginInfo(formInfo);
        }).catch(function (err) {
            setErrors(err.errors);
            return;
        });
    }

    return (
        <div className="auth-container animate__animated animate__slideInRight animate__faster">
            <div className="d-flex text-center">
                <div className="logo">
                    <img src={logo} alt="" />
                </div>
            </div>
            <div className="title text-center">
                Login
            </div>
            <div className="explain text-center mb-5">
                Register an account with MegaO for secured and speedy digital assets trading
            </div>

            {
                errors && <div className="errors">
                {
                    (isString(errors) && errors) && <><SvgIcon icon={<TimesCircle />}></SvgIcon> <p>{errors}</p></>
                }
                {
                    (isArray(errors) && errors.length > 0) && <>
                        <SvgIcon icon={<TimesCircle />}></SvgIcon> { errors.map((error, key) => {
                            return <p key={key} className='err'>{error}</p>
                        }) }
                    </>
                }
                </div>
            }
            
            <div className="form">
                <div className="form-group mb-4">
                    <label>Your Email</label>
                    <InputGroup onChange={(value) => handleChangeInfo('email', value)} prepend={<SvgIcon icon={<EmailIcon />} />} height={40} placeholder="example@email.com"/>
                </div>
                <div className="form-group mb-1">
                    <label>Your Password</label>
                    <InputGroup type="password" onChange={(value) => handleChangeInfo('password', value)} prepend={<SvgIcon icon={<PasswordIcon />} />} height={40} placeholder="********"/>
                </div>
                <div className="form-group text-end mb-4">
                    <Link to="/reset_email"><strong>Forgot Password?</strong></Link>
                </div>

                <div className="form-group">
                    <Button 
                        type="primary" 
                        classList="position-relative" 
                        style={{width: "100%"}}
                        onClick={() => handleSubmit()}
                        >
                        Next <SvgIcon icon={<SortRight />} size={9} style={{position: 'absolute', right: '8px'}} color="#fff"></SvgIcon>
                        </Button>
                </div>

                <div className="register-footer d-flex align-items-center mt-3">
                    New on MegaO? <Link to="/register" className='d-flex align-items-center mx-3'><SvgIcon icon={<SortRight />} size={6} color="#076BFF" style={{marginRight: '4px'}}></SvgIcon> Create an Account</Link>
                </div>
            </div>
        </div>
    );
}

export default Login;