import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import './style.scss';
import logo from 'statics/images/logo.png';
import SvgIcon from 'components/icons/SvgIcon';
import {EmailIcon, PasswordIcon, SortRight, TimesCircle} from 'components/icons/svgs';
import { Button } from 'components/button';
import { Link } from 'react-router-dom';
import { Input, InputGroup } from 'components/input';
import * as Yup from 'yup';
import { Trans, t } from '@lingui/macro';
import { trans } from 'config/LinguiConfig';
import {selectAuth, actions} from 'redux/authSlide';
import { useDispatch, useSelector } from 'react-redux';
import { isArray, isString } from 'utils/validator';
import { MessageBox, ModalBox, WaitingBox } from 'components/dialog';
import queryString from 'query-string';
import * as apis from 'services/api';
import ValidateResetEmailSuccess from './ValidateResetEmailSuccess';
import {md5} from 'utils/externals';

ValidateResetEmail.propTypes = {
    
};

function ValidateResetEmail(props) {
    const [errors, setErrors] = useState([]);
    const [formInfo, setFormInfo] = useState({});
    const [resetSuccess, setResetSuccess] = useState(false);
    const [queryParams, setQueryParams] = useState({});
    const authState = useSelector(selectAuth);

    useEffect(() => {
        const query = queryString.parse(window.location.search);
        setQueryParams(query);
    }, [])

    useEffect(() => {
        setErrors(authState.error);
    }, [authState.error]);

    const schema = Yup.object().shape({
        newPassword: Yup.string()
            .required(trans(t`Vui lòng nhập mật khẩu`))
            .matches(
            /^(?![0-9]+$)(?![a-zA-Z]+$)[\S]{6,}$/,
            trans(t`Độ dài mật khẩu phải lớn hơn hoặc bằng 6 và bao gồm cả chữ và số!`)
            ),
        newPassword2: Yup.string()
            .oneOf([Yup.ref('newPassword'), null], trans(t`Mật khẩu xác nhận không đúng`))
            .required(trans(t`Vui lòng nhập mật khẩu xác nhận`))
    });

    const handleChangeInfo = (field = '', value = '') => {
        const newData = {};
        newData[field] = value;

        setFormInfo({
            ...formInfo,
            ...newData
        });
    }

    const handleSubmit = async() => {
        schema.validate(formInfo).then(async () => {
            await apis.sendResetPassword({
                ...formInfo,
                newPassword: md5(formInfo.newPassword),
                newPassword2: md5(formInfo.newPassword2),
                uuid: queryParams.uuid
            }).then((res) => {
                setResetSuccess(true);
            }).catch(err => {
                setErrors(err);
            })
        }).catch(function (err) {
            setErrors(err.errors);
        });
    }

    return (
        <div className='auth-page'>
            <div className="container-fluid">
                <div className="auth-form">
                    {!resetSuccess ? <div className="auth-container animate__animated animate__slideInRight animate__faster">
                        <div className="d-flex text-center">
                            <div className="logo">
                                <img src={logo} alt="" />
                            </div>
                        </div>
                        <div className="title text-center">
                            Đặt mật khẩu mới
                        </div>
                        <div className="explain text-center mb-5">
                            Vui lòng đặt lại mật khẩu của bạn!
                        </div>

                        {
                            errors && <div className="errors">
                            <SvgIcon icon={<TimesCircle />}></SvgIcon>
                            {
                                isString(errors) && <p>{errors}</p>
                            }
                            {
                                isArray(errors) && errors.map((error, key) => {
                                    return <p key={key} className='err'>{error}</p>
                                })
                            }
                            </div>
                        }
                        
                        <div className="form">
                            <div className="form-group mb-4">
                                <label>New password</label>
                                <InputGroup type="password" onChange={(value) => handleChangeInfo('newPassword', value)} prepend={<SvgIcon icon={<PasswordIcon />} />} height={40} placeholder="New password"/>
                            </div>
                            <div className="form-group mb-4">
                                <label>Confirm new password</label>
                                <InputGroup type="password" onChange={(value) => handleChangeInfo('newPassword2', value)} prepend={<SvgIcon icon={<PasswordIcon />} />} height={40} placeholder="Re-enter new password"/>
                            </div>
                            <div className="form-group">
                                <Button 
                                    type="primary" 
                                    classList="position-relative" 
                                    style={{width: "100%"}}
                                    onClick={() => handleSubmit()}
                                    >Send email <SvgIcon icon={<SortRight />} size={9} style={{position: 'absolute', right: '8px'}} color="#fff"></SvgIcon>
                                    </Button>
                            </div>
                        </div>
                    </div> : <ValidateResetEmailSuccess />}
                </div>
            </div>
        </div>
    );
}

export default ValidateResetEmail;