import React, { Component, Children, isValidElement, cloneElement } from 'react';
import PropTypes from 'prop-types';
import { uuid } from 'utils/externals';

import './style.scss';

class RadioGroup extends Component {
	static propTypes = {
		horizontal: PropTypes.bool,
		value: PropTypes.oneOfType([
			PropTypes.string,
			PropTypes.number,
			PropTypes.bool
		]),
		style: PropTypes.object,
		onChange: PropTypes.func
	}

	static defaultProps = {
		horizontal: false,
		value: null,
		style: {},
		onChange() {}
	}

	constructor(props) {
		super(props);

		this.id = uuid();

		this.renderRadioButton = this.renderRadioButton.bind(this);
		this.handleChange = this.handleChange.bind(this);
	}

	handleChange(val) {
		const { onChange } = this.props;

		onChange(val);
	}

	renderRadioButton() {
		const { children, value } = this.props;
		const buttons = [];

		Children.forEach(children, (child, idx) => {
			if (isValidElement(child)) {
				if (child.type.displayName === 'Radio') {
					buttons.push(cloneElement(
						child,
						{
							...child.props,
							key: idx,
							checked: value === child.props.value,
							groupId: this.id,
							onChange: this.handleChange
						}
					));
				}
			}
		});

		return buttons;
	}

	render() {
		const { horizontal, style } = this.props;
		
		return (
			<div
				className={`ui-radio radio-group ${horizontal ? 'radio-horizontal' : ''}`}
				style={style}
			>
				{ this.renderRadioButton() }
			</div>
		);
	}
}

export default RadioGroup;