import { isDefined, isEmpty } from './validator';

// Hàm parser json. Mặc định trả về null
export function jsonParse(json, defaultVal = null) {
	try {
		return JSON.parse(json);
	} catch (ex) {
		return defaultVal;
	}
}

// Hàm parser int. Mặc định trả về null
export function intParse(val, defaultVal) {
	let value;

	if (isDefined(val) && !isEmpty(val) && !isNaN(val)) {
		try {
			value = parseInt(val);
		} catch (ex) {
			value = isDefined(defaultVal) ? defaultVal : null;
		}
	} else {
		value = isDefined(defaultVal) ? defaultVal : null;
	}

	return value;
}

// Hàm parser number. Mặc định trả về null
export function numberParse(val, defaultVal) {
	let value;

	if (isDefined(val) && !isEmpty(val) && !isNaN(val)) {
		try {
			value = Number(val) !== NaN ? Number(val) : (defaultVal || null);
		} catch (ex) {
			value = isDefined(defaultVal) ? defaultVal : null;
		}
	} else {
		value = isDefined(defaultVal) ? defaultVal : null;
	}

	return value;
}