import GlobalStore from 'constants/global';
import moment from 'moment';
import { isEmpty } from 'utils/validator';

export default class LocalStorage {
  static clearLocal = async () => {
    LocalStorage.clearToken();
  };

  static clearToken = async () => {
    await LocalStorage.setToLocal('guestToken', null);
    await LocalStorage.setToLocal('token', null);
    await LocalStorage.setToLocal('refreshToken', null);
    await LocalStorage.setToLocal('tokenExpired', null);
    await LocalStorage.setToLocal('refreshTokenExpired', null);
    await LocalStorage.setToLocal('userId', null);
    await LocalStorage.setToLocal('showIntroCashTrade', null);
  };

  static setToLocal = async (key, value) => {
    try {
      await localStorage.setItem(key, JSON.stringify(value));
    } catch (error) {
      console.log('LocalStorage SET ' + key + ' FROM LOCAL \n ERROR:', error.message);
    }
  };

  static getFromLocal = async (key) => {
    let data = '';
    try {
      let res = await localStorage.getItem(key);
      data = JSON.parse(res);
    } catch (error) {
      console.log('LocalStorage GET ' + key + ' FROM LOCAL \n ERROR:', error.message);
    }

    return data;
  };

  static getUuid = async () => {
    const data = await LocalStorage.getFromLocal('uuid');

    return data || null;
  };

  static setUuid = async (uuid = null) => {
    await LocalStorage.setToLocal('uuid', uuid);
  };

  static getToken = async () => {
    const data = await LocalStorage.getFromLocal('token');
    return data || null;
  };

  static setToken = async (token = null) => {
    await LocalStorage.setToLocal('token', token);
  };

  static getGuestToken = async () => {
    const data = await LocalStorage.getFromLocal('guestToken');
    return data || null;
  };

  static setGuestToken = async (token = null) => {
    await LocalStorage.setToLocal('guestToken', token);
  };

  static getRefreshToken = async () => {
    const data = await LocalStorage.getFromLocal('refreshToken');
    return data || null;
  };

  static setRefreshToken = async (token = null) => {
    await LocalStorage.setToLocal('refreshToken', token);
  };

  static getTokenExpired = async () => {
    const data = await LocalStorage.getFromLocal('tokenExpired');
    return data || null;
  };

  static setTokenExpired = async (time = null) => {
    await LocalStorage.setToLocal('tokenExpired', time);
  };

  static getRefreshTokenExpired = async () => {
    const data = await LocalStorage.getFromLocal('refreshTokenExpired');
    return data || null;
  };

  static setRefreshTokenExpired = async (time = null) => {
    await LocalStorage.setToLocal('refreshTokenExpired', time);
  };

  static getLanguage = async () => {
    const lang = await LocalStorage.getFromLocal('lang');
    return lang || '';
  };

  static setLanguage = async (value = 'en') => {
    await LocalStorage.setToLocal('lang', value);
  };

  static getUserId = async () => {
    const id = await LocalStorage.getFromLocal('userId');
    return id || null;
  };

  static setUserId = async (value) => {
    await LocalStorage.setToLocal('userId', value);
  };

  static setUserGeo = async (userGeo) => {
    await LocalStorage.setToLocal('userGeo', JSON.stringify(userGeo));
    GlobalStore.currencyCode = userGeo.currencyCode;
    GlobalStore.countryCode = userGeo.countryCode;
  };

  static getUserGeo = async () => {
    const userGeo = await LocalStorage.getFromLocal('userGeo');
    return JSON.parse(userGeo) || null;
  };

  static setUser = async (user) => {
    await LocalStorage.setToLocal('user', user);
    GlobalStore.user = user;
  };

  static getUser = async () => {
    const userInfo = await LocalStorage.getFromLocal('user');
    return JSON.parse(userInfo) || null;
  };

  static setDataList = async (key, data = { data: [], expiredTime: '' }) => {
    await LocalStorage.setToLocal(key, JSON.stringify(data));
  };

  static getDataList = async (key = '') => {
    try {
      const dataStr = await LocalStorage.getFromLocal(key);

      const data = JSON.parse(dataStr);
      if (!isEmpty(data) && !isEmpty(data.data) && moment().diff(moment(data.expiredTime)) < -300000) {
        return data.data;
      }
      return null;
    } catch (error) {
      return null;
    }
  };

  static setIntroCashTrade = async (value = 0) => {
    await LocalStorage.setToLocal('showIntroCashTrade', value);
  };
}
