import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Button, ButtonGroup } from 'components/button';
import SvgIcon from 'components/icons/SvgIcon';
import { BitcoinIcon, CurrencyRateIcon, SortDown, SortRight } from 'components/icons/svgs';
import { Dropdown, DropdownItem, DropdownMenu, DropdownToggle } from 'components/dropdown';
import { FontAwesome } from 'components/icons';
import { Link } from 'react-router-dom';
import { Input } from 'components/input';
import PaymentMedodField from './PaymentMedodField';
import CurrencySelectField from './CurrencySelectField';
import CoinSelectField from './CoinSelectField';

import './style.scss';

SearchForm.propTypes = {
    
}; 

function SearchForm(props) {
    const labels = {
        'sell': {
            text: 'Buy',
            payment: 'Get paid via',
            currency: 'I Want To Get',
        },
        'buy': {
            text: 'Sell',
            payment: 'Pay via',
            currency: 'I Want To Spend',
        }
    }

    const [type, setType] = useState('buy');
    const [labelByType, setTypeLabelByType] = useState(labels[type]);

    const handleChangeType = () => {
        if(type === 'buy'){
            setType('sell');
        }else{
            setType('buy');
        }
    }

    useEffect(() => {
        setTypeLabelByType(labels[type]);
    }, [type]);

    const handleCoinChange = () => {

    }
    
    const handleMethodChange = () => {

    }
    
    const handleCurrencyChange = () => {

    }

    const handleSubmit = () => {

    }

    return (
        <div className='section-search'>
                <div className="container">
                    <div className="row flex-column flex-lg-row">
                        <div className='col-lg-6 col-xl-7 order-lg-1 text-end text-desc'>
                            <h1>New generation trading</h1>
                            <h4>Easy to trade and store cryptocurrencies on any platform in just a few minutes. </h4>
                        </div>
                        <div className='col-lg-6 col-xl-5 order-lg-2'>
                            <div className="search-form-container">
                                <div className="form-group">
                                    <div className='d-flex justify-content-between align-items-center mb-2 font-weight-bold'>
                                        <label className='text-capitalize'>{type}</label>
                                        <span className='d-flex align-items-center' style={{color: '#0034C6', cursor: 'pointer'}} onClick={() => handleChangeType()}><span style={{marginRight: '9px'}}>{labelByType['text']}</span> <SvgIcon icon={<SortRight size={6}/>} size={6} color="#0034C6" /></span>
                                    </div>
                                    <div className='mb-1'>
                                        <CoinSelectField onChange={() => handleCoinChange}/>
                                    </div>
                                    <div className="mb-3">
                                        <span>1 BTC = <span style={{color: '#0034C6', marginRight: '10px'}}>1,125,507,733.18 VND</span> <SvgIcon icon={<CurrencyRateIcon />} color="#0034C6" size={16}/> </span>
                                    </div>
                                    <div className='d-flex justify-content-between align-items-center mb-2 font-weight-bold'>
                                        <label>{labelByType['payment']}</label>
                                    </div>
                                    <div className='mb-2'>
                                        <PaymentMedodField onChange={() => handleMethodChange} />
                                    </div>
                                    <div className="mb-3">
                                        <Button type="default" outline={true} height={20} size="small" style={{paddingLeft: '6px', paddingRight: '6px'}}>Bank Tranfers</Button>
                                        <Button type="default" outline={true} height={20} size="small">Paypal</Button>
                                    </div>
                                    <div className='d-flex justify-content-between align-items-center mb-2 font-weight-bold'>
                                        <label>{labelByType['currency']}</label>
                                    </div>
                                    <div className='mb-1'>
                                        <CurrencySelectField onChange={() => handleCurrencyChange} />
                                    </div>
                                    <div className="mb-4">
                                        <span>Minimum: 228207 VND</span>
                                    </div>
                                    <div className='mb-0'>
                                        <Button type="primary" height={46} classList="w-100 justify-content-between" style={{
                                            textTransform: 'capitalize', 
                                            padding: '10px 16px', 
                                            borderRight: '1px solid #D7D7D7', 
                                            fontSize: '18px'
                                        }} onClick={() => handleSubmit}>
                                            <span>Find Offers</span><i className="fal fa-search"></i>
                                        </Button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
        </div>
    );
}

export default SearchForm;