import React, { Component } from 'react';
import Scrollbars from 'react-custom-scrollbars-2';
import { isBoolean, isFunction } from 'utils/validator';
import PropTypes from 'prop-types';

class Scrollbar extends Component {
	static propTypes = {
		visible: PropTypes.bool,
		nativeRef: PropTypes.func
	}

	static defaultProps = {
		visible: true,
		nativeRef() {}
	}

	constructor(props) {
		super(props);
	}

	reset = () => {
		if (this.refScroll) {
			this.refScroll.scrollTop(0);
			this.refScroll.scrollLeft(0);
		}
	}

	render() {
		const { children, visible, nativeRef, ...props } = this.props;

		if (isBoolean(visible) && !visible) {
			return (
				<div style={{
					overflow: 'hidden',
					width: '100%',
					height: '100%'
				}}>
					{ children }
				</div>
			);
		}

		props.ref = c => {
			this.refScroll = c;
			
			if (isFunction(nativeRef)) {
				nativeRef(c);
			}
		};

		return (
			<Scrollbars {...props} universal={true} >
				{ children }
			</Scrollbars>
		);
	}
}

export default Scrollbar;