import { Component } from "react";
import PropTypes from 'prop-types';

import { Button } from 'components/button';
import { FontAwesome } from "components/icons";

import "./style.scss";
import { clsx } from "utils/externals";

class Dropdown extends Component {
    static propTypes = {
        toggle: PropTypes.any,
        actived: PropTypes.string,
        data: PropTypes.array,
        onChange: PropTypes.func,
        style: PropTypes.object,
        btnStyle: PropTypes.object,
        classList: PropTypes.string
    }

    static defaultProps = {
        toggle: <FontAwesome icon="fa fa-ellipsis-v" color="#C4C4C4"/>,
        actived: null,
        data: [],
        style: {},
        btnStyle: {},
        classList: '',
		onChange() {}
    }

    constructor(props){
        super(props);

        this.state = {
            open: false
        }
    }

    componentDidMount () {
        const { open } = this.state;
        document.addEventListener('click', this.handleClickOutside);
    }

    componentWillUnmount() {
        const { open } = this.state;
        document.removeEventListener('click',  this.handleClickOutside);
    }

    handleClickOutside = event => {
        if (this.refWrapper && !this.refWrapper.contains(event.target)) {
            this.setState({
                open: false
            })
        }
    }

    handleClickToggle = (e) => {
        e.stopPropagation();
        
        const { open } = this.state;

        this.setState({
            open: !open
        })
    }

    handleSelect = (data) => {
        const { onChange } = this.props;

        onChange(data);
        this.setState({
            open: false
        })
    }

    render() {
        const { toggle, data, style, classList, btnStyle } = this.props;
        const { open } = this.state;
        
        return <div className={clsx('ui-dropdown', classList)} ref={c => this.refWrapper = c} style={style}>
            <div className="ui-dropdown-toggle" ref={c => this.refToggle = c} onClick={(e) => this.handleClickToggle(e)}>
                { toggle }
            </div>
            <div className={clsx("ui-dropdown-list", open && "open")}>
                <div className="ui-dropdown-list-header">{}</div>
                {
                    data.map((item, idx) => {
                        return (
                            <div className="ui-dropdown-item" key={`ui-dropdown-item-${idx}`}>
                                <Button
                                    key={`ui-dropdown-item-btn-${idx}`}
                                    radius={5}
                                    bold={true}
                                    height={30}
                                    width={107}
                                    bgColor="white"
                                    textColor={item.color}
                                    icon={item.icon}
                                    style={{
                                        // border: "1px solid rgba(0,0,0,.1)",
                                        // boxShadow: "0 0 20px rgba(0,0,0,.1)",
                                        width: '100%',
                                        ...btnStyle
                                    }}
                                    onClick={() => this.handleSelect({
                                        type: item.type,
                                        value: item.value
                                    })}
                                >
                                {item.elm}
                                </Button>
                            </div>
                        )
                    })
                }
            </div>
        </div>
    }
}

export default Dropdown;