import axios from 'axios';
import LocalStorage from 'config/localStorage';
import { BASE_URL } from 'constants/constants';
import GlobalStore from 'constants/global';
import { logout } from 'services/api';
import { uuid } from './externals';

const TIME_OUT = 1800;

export const resetToken = async (callBack) => {
  const refreshAToken = await GlobalStore.refreshToken;
  GlobalStore.token = null;

  if (refreshAToken) {
    const uuid = GlobalStore.uuid;

    const data = {
      uuid: uuid,
      refreshToken: refreshAToken
    };

    const headers = {
      Authorization: 'Bearer ' + refreshAToken,
      uuid: uuid
    };

    return axios({
      url: BASE_URL + '/users/refresh-access-token',
      method: 'post',
      headers,
      data,
      timeout: TIME_OUT
    })
      .then(async response => {
        //  console.log('refresh token', response)
        if (response?.data) {
          const { data: d } = response;
          const { code, data } = d;
          if (code < 300) {
            const { refreshToken, token, token_expire } = data;
            await LocalStorage.setGuestToken(null);
            await LocalStorage.setToken(token);
            await LocalStorage.setTokenExpired(token_expire);
            await LocalStorage.setRefreshToken(refreshToken);

            GlobalStore.token = await token;
            GlobalStore.isLoginUser = await true;
            GlobalStore.refreshToken = await refreshToken;
            GlobalStore.tokenExpired = await token_expire;
            // if (callBack) {
            //     setTimeout(() => {
            //         callBack()
            //     }, 250)
            // }
            return true;
          } else {
            signOut(true);
            return false;
          }
        } else {
          signOut(true);
          return false;
        }
      })
      .catch(error => {
        console.log(error);
        signOut(true);
        return false;
      });
  }
};

export const getAnoymousToken = async () => {
  GlobalStore.token = null;
  GlobalStore.isEscrowGuest = false;

  let uuid = GlobalStore.uuid;

  if(!uuid){
    uuid = await uuid();
  }

  let headers = { uuid };
  
  return axios({
    url: BASE_URL + '/common/tk',
    method: 'post',
    headers,
    timeout: TIME_OUT
  })
    .then(async response => {
      if (response) {
        const { data: d } = response;
        const { code, data } = d;
        if (code < 300) {
          const { token, token_expire } = data;

          GlobalStore.token = await token;
          GlobalStore.tokenExpired = await token_expire;
          
          await LocalStorage.setGuestToken(token);
          await LocalStorage.setTokenExpired(token_expire);

          return true;
        }
        return false;
      }
    })
    .catch(error => {
      console.log(error);
      // signOut(true);
      return false;
    });
};


export const signOut = async (force = false) => {
  GlobalStore.appLoading = 0.4;
  // const { user = {} } = store.getState().user;

  if (!force) {
    try {
      await logout();
    } catch (error) {
      console.log(error);
    }
  }
  await LocalStorage.setToken(null);
  await LocalStorage.setUserId(null);
  await LocalStorage.setIntroCashTrade(null);
  await LocalStorage.setTokenExpired(null);
  await LocalStorage.setRefreshToken(null);
  GlobalStore.token = null;
  GlobalStore.isLoginUser = null;
  GlobalStore.refreshToken = null;
  GlobalStore.tokenExpired = null;

  // handleEmitStatusUser(false, user.fid);
  // store.dispatch(app.actions.goToHome());
  // store.dispatch({ type: getTypeClear(types.user) });

  return true;
};