import { createSlice } from '@reduxjs/toolkit';
import LocalStorage from 'config/localStorage';

const initialState = {
  coinList: [],
  currencyList: [],
  countryList: [],
  paymentMethodList: [],
};

export const appSlide = createSlice({
  name: 'app',
  initialState,
  reducers: {
    changeLanguage: (payload) => {
      LocalStorage.setLanguage(payload);
      return payload;
    },

    getCoinList: (payload) => payload,
    getCoinListSuccess: (state, {payload}) => {
      state.coinList = payload;
    },

    getPaymentMethodList: (payload) => payload,
    getPaymentMethodListSuccess: (state, {payload}) => {
      state.paymentMethodList = payload;
    },
    
    getCurrencyList: (payload) => payload,
    getCurrencyListSuccess: (state, {payload}) => {
      state.currencyList = payload;
    },

    getCountryList: (payload) => payload,
    getCountryListSuccess: (state, {payload}) => {
      state.countryList = payload;
    }
  },
});

export const actions = appSlide.actions;

// Selectors
export const selectCoinList = (state) => state.app.coinList;
export const selectCurrencyList = (state) => state.currencyList;
export const selectCountryList = (state) => state.countryList;
export const selectPaymentMethodList = (state) => state.paymentMethodList;

export default appSlide.reducer;
